.project {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &_wrap {
        height: 100%;
        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: var(--rowContent3);
        }
    }

    &_lf {
        width: calc(100% * 4 / 12);

        @media screen and (max-width: 1200px) {
            width: calc(100% * 3.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: calc(100% * 4 / 12);
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &--wrap {
            padding:0 2.4rem;
            position: sticky;
            top: 10rem;

            @media screen and (max-width: 1200px) {
                padding: 0;
            }
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            @media screen and (max-width: 600px) {
                flex-direction: row;
                flex-wrap: wrap;
            }
        }

        &--item {
            &.actived {
                .project_lf--link {
                    background: #3bbdba1a;
                    font-weight: 700;
                    color: $color-pri;
                }
            }

            @media screen and (max-width: 600px) {
                width: calc(100% / 4);
            }

            @media screen and (max-width: 460px) {
                width: calc(100% / 3);
            }

            @media screen and (max-width: 320px) {
                width: calc(100% / 2);
            }
        }

        &--link {
            border-radius: var(--radius16);
            padding: 1.6rem 3.2rem 1.6rem 1.6rem;
            color: $color-gray3;
            display: block;
            position: relative;
            cursor: pointer;
            transition: 0.1s linear;
            background: transparent;
            @include fz-16;
            @include hover {
                background: #3bbdba1a;
                color: $color-pri;
            }

            @media screen and (max-width: 1200px) {
                padding: 1.4rem 3rem 1.4rem 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem 3rem 1.2rem 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem;
            }

            @media screen and (max-width: 460px) {
                padding: 0.8rem;
                @include fz-14;
            }

            &::before {
                content: "";
                position: absolute;
                background-image: url(../assets/images/AltArrowRight.svg);
                top: 50%;
                right: 1.6rem;
                transform: translateY(-50%);
                width: 1.6rem;
                height: 1.6rem;

                @media screen and (max-width: 1200px) {
                    right: 1.4rem;
                }

                @media screen and (max-width: 800px) {
                    right: 1.2rem;
                }

                @media screen and (max-width: 600px) {
                    display: none;
                }
            }
        }
    }

    &_rt {
        width: 100%;
        flex: 1;

        &--list {
            row-gap: var(--rowContent3);
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent6);
        }

        &--item {
            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &_item {
        height: 100%;
        @include hover {
            .project_item--top {
                .title-sm {
                    color: $color-pri;
                }
            }

            .project_item--img {
                &::before {
                    animation: shine 1s;
                }

                img {
                    transform: translate(-50%, -50%) scale(1.1);
                }
            }
        }
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            border-radius: var(--radius16) var(--radius16) 0 0;
            background: $color-white;
            overflow: hidden;
        height: 100%;

        }

        &--top {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
            .dm {
                color: $color-text4;
                line-height: 1.3;
                display: block;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                @include hover {
                    color: $color-pri;
                }
            }
        }

        &--bottom {
            .note-text {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }

        &--content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            height: 100%;
            .link {
                text-decoration: underline;
                margin-top: auto;
                @include hover {
                    color: $color-second;
                }
            }
        }

        &--img {
            padding-top: 100%;
            display: block;
            position: relative;
            overflow: hidden;
            width: 100%;

            @include hoverTG();

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                transform: translate(-50%, -50%);
                top: 50%;
                left: 50%;
                object-fit: cover;
            }
        }
    }
}

.project_ct {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pd);
    background: #f5f7f7;

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent6);
    }

    &--bg {
        width: 95%;
        margin: 0 auto;
        border-radius: var(--radius3);
        overflow: hidden;

        @media screen and (max-width:1200px) {
            width: 100%;
            border-radius: 0;
        }
    }

    &--img {
        padding-top: calc(100% * 902 / 1648);
        width: 100%;
        position: relative;
        display: block;
        overflow: hidden;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &--content {
        padding: 3.2rem 3.2rem 3.2rem 0;
        border-right: 0.1rem solid #d0d9d9;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        height: 100%;

        @media screen and (max-width: 1200px) {
            padding: 2.4rem 2.4rem 2.4rem 0;
        }

        @media screen and (max-width: 800px) {
            padding: 2rem 2rem 2rem 0;
        }

        @media screen and (max-width: 600px) {
            padding: 1.6rem 0 1.6rem 0;
            border-top: unset;
            border-left: unset;
            border-right: unset;
            border-bottom: 0.1rem solid #d0d9d9;
        }
    }

    &--des {
        &-box {
            padding: 0 3.2rem;
            background: $color-white;
            border-radius: var(--radius3);
            overflow: hidden;
            border: 0.1rem solid #d0d9d9;

            @media screen and (max-width: 1200px) {
                padding: 0 2.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0 2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0 1.6rem;
            }

            @media screen and (max-width: 460px) {
                padding: 0 1.2rem;
            }
        }
        &-wrap {
            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }

        &-lf {
            width: calc(100% * 5 / 12 + 2.4rem);

            @media screen and (max-width: 1200px) {
                width: calc(100% * 6 / 12);
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }

        &-rt {
            width: 100%;
            flex: 1;
        }

        &-list {
            padding: 3.2rem 0 3.2rem 0.8rem;
            display: flex;
            column-gap: var(--columnContent2);
            row-gap: var(--rowContent);
            flex-wrap: wrap;
            justify-content: space-between;

            @media screen and (max-width: 1200px) {
                padding: 2.4rem 0 2.4rem 0rem;
            }

            @media screen and (max-width: 800px) {
                padding: 2rem 0 2rem 0rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.6rem 0 1.6rem 0rem;
            }
        }

        &-item {
            width: calc(50% - (var(--columnContent2) / 2));
            display: flex;
            align-items: center;
            column-gap: var(--columnContent0);

            .ic {
                width: 4.8rem;
                height: 4.8rem;
                border-radius: 50%;
                overflow: hidden;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;

                @media screen and (max-width: 800px) {
                    width: 4rem;
                    height: 4rem;
                }

                @media screen and (max-width: 460px) {
                    width: 3.6rem;
                    height: 3.6rem;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .content {
                display: flex;
                flex-direction: column;
                row-gap: 0.2rem;

                .note {
                    color: $color-gray6;
                }
            }
        }
    }
}

.album {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        position: relative;

        .btn {
            position: absolute;
            bottom: 2%;
            right: 2%;
            z-index: 2;
        }
    }

    &_list {
        row-gap: var(--rowContent3);
    }

    &_img {
        border-radius: var(--radius3);
        padding-top: 100%;
        position: relative;
        display: block;
        overflow: hidden;
        width: 100%;
        pointer-events: none;
        // @include hoverTG();

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &_item {
        display: none;
        &:first-child {
            display: block;
        }

        &:nth-child(2) {
            display: block;
        }

        &:nth-child(3) {
            width: 100%;
            display: block;

            .album_img {
                padding-top: calc(100% * 592 / 1217);

                @include hover {
                    &::before {
                        animation: shine 2s;
                    }
                }
            }
        }

        &:nth-child(4) {
            width: 100%;
            display: block;

            .album_img {
                padding-top: calc(100% * 592 / 1217);

                @include hover {
                    &::before {
                        animation: shine 2s;
                    }
                }
            }
        }
    }
}

.project_more {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        .swiper-slide {
            height: auto;

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &--item {
        height: 100%;
    }
}
