.header {
  position: fixed;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  z-index: 2002;
  transition: 0.3s linear;

  &-wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    column-gap: 1.4rem;
    height: 100%;
    // justify-content: space-between;
    width: 100%;

    transition: all 0.3s linear;
  }

  &-top {
    width: 100%;
    height: 4rem;
    background-color: $color-black;
    transition: height 0.3s linear;
    &-wrap {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.3s linear;
      .note-md {
        text-align: center;
        transition: 0.3s linear;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        @media screen and (max-width: 600px) {
          @include fz-16;
        }

        @media screen and (max-width: 400px) {
          @include fz-10;
        }

        @media screen and (max-width: 360px) {
          @include fz-10;
        }

        @media screen and (max-width: 320px) {
          font-size: 0.8rem;
        }
      }
    }

    @media screen and (max-width: 1200px) {
      display: block;
      height: 3rem;
    }

    @media screen and (max-width: 460px) {
      height: 3rem;
    }
  }

  &-bottom {
    background: $color-white;
    width: 100%;
    display: flex;
    justify-content: center;
    height: 10rem;
    position: relative;
    transition: 0.3s linear;

    .container {
      padding: 0 6.4rem;
      @media screen and (max-width: 1400px) {
        padding: 0 4.8rem;
      }

      @media screen and (max-width: 1200px) {
        padding: 0 1.5rem;
      }
    }
    &-wrap {
      display: flex;
      width: 100%;
      height: 100%;
      margin: auto;
      column-gap: 2rem;
      justify-content: space-between;

      @media screen and (max-width: 1200px) {
        width: 100%;

        align-items: center;
      }
    }

    &-lf {
      display: flex;
      column-gap: 6.4rem;

      @media screen and (max-width: 1200px) {
        display: none;
      }
    }

    &-rt {
      display: flex;
      column-gap: 6.4rem;

      @media screen and (max-width: 1200px) {
        display: none;
      }
    }

    @media screen and (max-width: 1200px) {
      height: 7rem;
    }

    @media screen and (max-width: 600px) {
      height: 6rem;
    }

    @media screen and (max-width: 460px) {
      height: 5rem;
    }
  }

  &-contact {
    .note-md {
      color: $color-pri;
      display: flex;
      align-items: center;
      height: 100%;
      transition: all 0.3s linear;
      strong {
        color: $color-second;
        transition: all 0.3s linear;
      }

      @include hover {
        color: $color-second;

        strong {
          color: $color-pri;
        }
      }
    }
  }

  &.actived {
    height: auto;
    box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);
    .header-logo {
      transform: translate(-50%, -50%) scale(0.65);
    }

    .header-top {
      transition: height 0.3s linear;
      height: 3.5rem;
      .header-top-wrap {
        .note-md {
          transform: scale(0.9);
        }
      }
    }

    .header-bottom {
      height: 7rem;
    }

    @media screen and (max-width: 1200px) {
      height: 7rem;
    }

    @media screen and (max-width: 1200px) {
      & ~ .menu-mb {
        top: 7rem;
        height: calc(100% - 7rem);
      }
    }
  }

  &-nav {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.2rem;

    @media screen and (max-width: 1200px) {
      .menu-list {
        display: none;
      }
    }
  }

  &-logo {
    max-width: 16.5rem;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
    transition: 0.3s linear;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include hover {
      img {
        transform: scale(0.9);
      }
    }

    img {
      width: 100%;
      height: 100%;
      transition: all 0.3s linear;
      object-fit: cover;
    }

    @media screen and (max-width: 1200px) {
      max-width: 10rem;
      position: relative;
      transform: unset;
      left: unset;
      top: unset;
    }

    @media screen and (max-width: 600px) {
      max-width: 8rem;
    }

    @media screen and (max-width: 460px) {
      max-width: 7.5rem;
    }
  }

  &-mobi {
    width: 3.2rem;
    height: 2.2rem;
    position: relative;
    display: none;

    cursor: pointer;
    @media screen and (max-width: 1200px) {
      display: block;
    }

    @media screen and (max-width: 460px) {
      width: 2.8rem;
      height: 2rem;
    }

    &.actived {
      .line {
        &:first-child {
          top: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          width: 100%;
          transition: top 0.3s linear, transform 0.3s linear 0.5s;
        }

        &:nth-child(2) {
          opacity: 0;
          transition: opacity 0.1s ease 0.3s;
        }

        &:last-child {
          bottom: 50%;
          transform: translate(-50%, 50%) rotate(-45deg);
          width: 100%;
          transition: bottom 0.3s linear, transform 0.3s linear 0.5s;
        }
      }
    }

    .line {
      width: 100%;
      display: block;
      height: 0.3rem;
      background-color: $color-black;
      transform: translateX(-50%);
      border-radius: 100rem;
      overflow: hidden;
      position: absolute;

      &:first-child {
        top: 0;
        left: 50%;
        width: 100%;

        transition: top 0.3s linear 0.5s, transform 0.3s linear 0s;
      }

      &:nth-child(2) {
        top: 50%;
        left: 0;
        width: 100%;
        transform: translate(0%, -50%);
        transition: opacity 0.1s ease 0.3s;
      }

      &:last-child {
        width: 100%;
        bottom: 0%;
        left: 50%;
        transition: bottom 0.3s linear 0.5s, transform 0.3s linear 0s;
      }
    }
  }

  &-box {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.6rem;

    @media screen and (max-width: 1200px) {
      column-gap: 1.4rem;
      // flex-direction: row-reverse;
    }

    @media screen and (max-width: 460px) {
      column-gap: 0.8rem;
    }
  }

  @media screen and (max-width: 1200px) {
    box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.5);
  }
}
