.portfolio {
  padding-top: var(--size-pd);
  padding-bottom: var(--size-pdl);

  &_wrap {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent);
  }

  &_list {
    row-gap: var(--rowContent2);
  }

  &_item {
    &--wrap {
      position: relative;
      border-radius: var(--radius3);
      overflow: hidden;

      @include hover {
        .portfolio_item--img {
          &::after {
            opacity: 1;
          }

          &::before {
            opacity: 0;
          }
        }
      }
    }

    &--title {
      max-width: 90%;
      width: 100%;
      left: 50%;
      transform: translateX(-50%);
      z-index: 2;
      position: absolute;
      bottom: 7%;

      .title-sm {
        text-align: center;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }

    &--img {
      padding-top: calc(100%);
      position: relative;
      overflow: hidden;
      width: 100%;
      display: block;
      border-radius: var(--radius3);

      &::before {
        content: "";
        inset: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background: linear-gradient(180.07deg, rgba(0, 0, 0, 0) 0.06%, #000000 99.94%);
        z-index: 1;
        transition: 0.3s linear;
        opacity: 1;
      }

      &::after {
        content: "";
        inset: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.4) 100%);
        opacity: 0;
        z-index: 1;
        transition: 0.3s linear;
      }

      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.portfolio_ct {
  padding-top: var(--size-pd);
  padding-bottom: var(--size-pdl);

  &--wrap {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent);
  }

  &--content {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent12);
    max-width: 100.8rem;
    width: 100%;
    margin: 0 auto;
  }

  &--list {
    row-gap: var(--rowContent2);
  }

  &--item {
    @media screen and (max-width: 460px) {
        width: 100%;
    }
  }

  &--img {
    padding-top: 100%;
    width: 100%;
    display: block;
    position: relative;
    overflow: hidden;
    border-radius: var(--radius3);

    position: relative;

    @include hoverTG();

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
