.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    column-gap: 1rem;
    height: 4.8rem;
    width: fit-content;
    padding: 0 3.2rem;
    min-width: 18rem;
    border-radius: var(--radius1);
    background: $color-pri;
    transition: 0.3s linear;
    position: relative;
    overflow: hidden;
    z-index: 5;

    @media screen and (max-width: 1200px) {
        height: 4.2rem;
        padding: 0 1.2rem;
        min-width: 14rem;
    }

    @media screen and (max-width: 800px) {
        height: 4rem;
        padding: 0 1.2rem;
        min-width: 12rem;
    }

    @media screen and (max-width: 600px) {
        height: 3.8rem;
        min-width: 10rem;
    }

    @media screen and (max-width: 460px) {
        height: 3.6rem;
        min-width: 8rem;
    }

    &.cl-2 {
        background: $color-second;

        .btn-text {
            color: $color-white;
        }

        @include hover {
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            background: $color-white;
            .btn-text {
                color: $color-pri;
            }

            .btn-ic {
                i {
                    color: $color-pri;
                }
            }
        }
    }

    &.trans {
        border: 0.1rem solid $color-pri;
        background: $color-white;

        .btn-text {
            color: $color-pri;
        }

        @include hover {
            // border-color: $color-pri;
            background: $color-pri;
        }
    }

    &-text {
        // text-transform: uppercase;
        font-weight: 700;
        color: $color-white;
        position: relative;
        z-index: 2;
        transition: 0.3s linear;
        white-space: nowrap;
        display: flex;
        align-items: center;
        justify-content: center;
        @include fz-16;
        @media screen and (max-width: 1200px) {
            @include fz-14;
        }
    }

    &-ic {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        i {
            transform: rotate(-45deg);
            font-size: 1.6rem;
            color: $color-white;
            line-height: 100%;
            transition: all 0.1s linear;

            @media screen and (max-width: 1200px) {
                @include fz-14;
            }
        }

        img {
            width: 2.4rem;
            height: 2.4rem;
        }
    }

    @include hover {
        background: $color-second;

        .btn-text {
            color: $color-white;
        }

        .btn-ic {
            img {
                filter: brightness(0) saturate(100%);
            }

            i {
                color: $color-white;
                transform: rotate(0);
            }
        }
    }
}

.box-navi {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: end;
    column-gap: var(--rowContent0);
    .btn-navi {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 4.8rem;
        height: 4.8rem;
        background: transparent;
        // position: absolute;
        border-radius: 0.5rem;
        border: 0.1rem solid $color-gray2;
        z-index: 5;
        cursor: pointer;
        transition: all 0.3s linear;
        // transform: translateY(-50%);
        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;
        }

        @media screen and (max-width: 800px) {
            width: 3.4rem;
            height: 3.4rem;
        }

        @media screen and (max-width: 600px) {
            width: 3.2rem;
            height: 3.2rem;
        }

        @media screen and (max-width: 460px) {
            width: 2.8rem;
            height: 2.8rem;
        }

        &.prev {
            // top: 50%;
            // left: -6rem;
        }

        &.next {
            // top: 50%;
            // right: -6rem;
        }

        @include hover {
            background: $color-pri;
            border-color: $color-pri;
            i {
                color: $color-white;
            }
        }

        i {
            @include fz-20;
            color: $color-second;
            transition: all 0.3s linear;

            @media screen and (max-width: 600px) {
                @include fz-14;
            }

            @media screen and (max-width: 460px) {
                @include fz-12;
            }
        }
    }

    @media screen and (max-width: 800px) {
        column-gap: var(--rowContent0);
    }
}

.box-navi-pagi {
    display: flex;
    align-items: center;
    column-gap: 2.4rem;
    width: fit-content;
    padding: 0.8rem;
    margin-top: 3rem;
    margin-left: 1rem;

    &.cl-2 {
        .btn-navi-2 {
            i {
                color: #f5d360;
            }
        }

        .swiper-pagination-bullet-active {
            background: #f5d360;
        }
    }

    .swiper-pagination {
        margin-top: 0;
    }

    .btn-navi-2 {
        cursor: pointer;
        i {
            font-size: 2rem;
            color: $color-pri;
        }
    }
}

.page-pagi {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: 4rem auto 0;
    column-gap: 0.5rem;
    &-wrap {
        display: flex;
        align-items: center;
        column-gap: 0.5rem;
    }
}

.number-pagi {
    cursor: pointer;
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    font-weight: 600;
    border-radius: 50%;
    color: $color-text;
    transition: all 0.3s linear;

    &.current {
        color: $color-pri;
    }

    @include hover {
        color: $color-pri;
    }

    &-ic.prev {
        background-color: $color-pri;
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        font-weight: 600;
        border-radius: 50%;
        transition: all 0.3s linear;

        i {
            color: #e9bc59;
            transition: all 0.3s linear;
        }
        @include hover {
            background: linear-gradient(180deg, #f5d461 54.62%, #fdc202 72.84%, #cb983f 88.46%);

            i {
                color: $color-white;
            }
        }
    }

    &-ic.next {
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-pri;
        font-weight: 600;
        border-radius: 50%;

        transition: all 0.3s linear;

        i {
            color: #e9bc59;
            transition: all 0.3s linear;
        }

        @include hover {
            background: linear-gradient(180deg, #f5d461 54.62%, #fdc202 72.84%, #cb983f 88.46%);

            i {
                color: $color-white;
            }
        }
    }
}

.box-navi-2 {
    display: flex;
    width: fit-content;
    // margin: 3rem 1.2rem 0 1.2rem;
    align-items: center;
    column-gap: 2rem;
    .btn-navi-2 {
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0.2rem solid $color-pri;
        transition: all 0.3s linear;
        cursor: pointer;
        position: absolute;
        z-index: 5;
        i {
            font-size: 2rem;
            color: $color-pri;
            transition: all 0.3s linear;
        }

        @include hover {
            background: $color-pri;

            i {
                color: #fdc202;
            }
        }

        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;

            i {
                font-size: 1.6rem;
            }
        }

        &.prev {
            top: 50%;
            transform: translateY(-50%);
            left: -5rem;

            @media screen and (max-width: 1380px) {
                left: -2rem;
            }

            @media screen and (max-width: 1300px) {
                left: 1rem;
            }

            @media screen and (max-width: 600px) {
                left: 0rem;
            }

            @media screen and (max-width: 460px) {
                left: -1rem;
            }
        }

        &.next {
            top: 50%;
            transform: translateY(-50%);
            right: -5rem;

            @media screen and (max-width: 1380px) {
                right: -2rem;
            }

            @media screen and (max-width: 1300px) {
                right: 1rem;
            }

            @media screen and (max-width: 600px) {
                right: 0rem;
            }

            @media screen and (max-width: 460px) {
                right: -1rem;
            }
        }
    }
}

.btn-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    @include hover {
        .btn-play-ic {
            background: $color-pri;
            i {
                color: $color-white;
            }
        }
    }

    &-tt {
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        @keyframes lanToa {
            0% {
                transform: translate(-50%, -50%) scale(1);
                opacity: 0;
            }
            50% {
                transform: translate(-50%, -50%) scale(1.2);
                opacity: 0.5;
            }
            100% {
                transform: translate(-50%, -50%) scale(1.4);
                opacity: 0;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 100%;
            transform: translate(-50%, -50%) scale3d(1);
            border-radius: 50%;
            background: $color-white;

            opacity: 0;
            animation: lanToa 2.4s linear infinite forwards;
        }

        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 100%;
            transform: translate(-50%, -50%) scale3d(1);
            border-radius: 50%;
            background: $color-white;

            opacity: 0;
            animation: lanToa 2.4s linear infinite forwards 1.2s;
        }
    }

    &-ic {
        position: relative;
        z-index: 2;
        width: 4rem;
        height: 4rem;
        flex-shrink: 0;
        background: $color-pri;
        border-radius: 50%;
        box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s linear;
        i {
            @include fz-16;
            color: $color-white;
            transition: 0.5s linear;
        }

        @media screen and (max-width: 1200px) {
            width: 3.6rem;
            height: 3.6rem;
        }

        @media screen and (max-width: 800px) {
            width: 3.2rem;
            height: 3.2rem;
        }

        @media screen and (max-width: 600px) {
            width: 3rem;
            height: 3rem;
        }
    }
}
