:root {
     --size-hd: 14rem;

     --size-pd: 4rem;

     --size-pdl: 8rem;
     --size-pdl2: 6rem;

     --font-pri: "Helvetica";
     --rowContent6: 6rem;
     --rowContent: 4rem;
     --rowContent3: 3.2rem;
     --rowContent2: 2.4rem;
     --rowContent1: 1.6rem;
     --rowContent0: 0.6rem;
     --rowContent12: 1.2rem;
     --columnContent3: 3.2rem;
     --columnContent0: 0.8rem;
     --columnContent2: 2.4rem;
     --columnContent1: 1.2rem;

     --radius0: 0.8rem;
     --radius1: 1.2rem;
     --radius16: 1.6rem;
     --radius3: 3.2rem;

     @media screen and (max-width: 1200px) {
          --size-pd: 3rem;
          --size-hd: 10rem;
          --size-pdl: 6rem;
          --size-pdl2: 4rem;

          --rowContent6: 4rem;
          --rowContent: 3rem;
          --rowContent3: 2.4rem;
          --rowContent2: 2rem;
          --rowContent1: 1.2rem;
          --rowContent0: 0.5rem;
          --rowContent12: 1rem;
          --columnContent3: 2.4rem;
          --columnContent0: 0.6rem;
          --columnContent2: 2rem;
          --columnContent1: 1rem;

          --radius0: 0.6rem;
          --radius1: 1rem;
          --radius16: 1.4rem;
          --radius3: 2.4rem;
     }

     @media screen and (max-width: 800px) {
          --size-pd: 2rem;
          --size-pdl: 4rem;

          --radius0: 0.4rem;
          --radius1: 0.8rem;
          --radius16: 1.2rem;
          --radius3: 1.8rem;

          --rowContent6: 3rem;
          --rowContent: 2.4rem;
          --rowContent3: 2rem;
          --rowContent2: 1.6rem;
          --rowContent1: 1rem;
          --rowContent0: 0.4rem;
          --rowContent12: 0.8rem;
          --columnContent3: 2rem;
          --columnContent0: 0.5rem;
          --columnContent2: 1.6rem;
          --columnContent1: 0.8rem;
     }

     @media screen and (max-width: 600px) {
          --size-pd: 1.6rem;
          --size-hd: 9rem;
          --size-pdl: 4rem;
          --size-pdl2: 4rem;

          --radius0: 0.4rem;
          --radius1: 0.8rem;
          --radius16: 1rem;
          --radius3: 1.4rem;

          --rowContent6: 2.4rem;
          --rowContent: 2rem;
          --rowContent3: 1.6rem;
          --rowContent2: 1.2rem;
          --rowContent1: 0.8rem;
          --rowContent0: 0.4rem;
          --rowContent12: 0.6rem;
          --columnContent3: 1.6rem;
          --columnContent0: 0.4rem;
          --columnContent2: 1.2rem;
          --columnContent1: 0.6rem;
     }

     @media screen and (max-width: 460px) {
          --size-hd: 8rem;

          --radius0: 0.4rem;
          --radius1: 0.8rem;
          --radius16: 1rem;
          --radius3: 1.2rem;

          --rowContent6: 2rem;
          --rowContent: 1.6rem;
          --rowContent3: 1.4rem;
          --rowContent2: 1.2rem;
          --rowContent1: 1rem;
          --rowContent0: 0.4rem;
          --rowContent12: 0.6rem;
          --columnContent3: 1.2rem;
          --columnContent0: 0.4rem;
          --columnContent2: 1rem;
          --columnContent1: 0.6rem;
     }

     @media screen and (max-width: 360px) {
          --rowContent1: 0.8rem;

     }
}

