.post_nb {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
}

.home_strong {
    // &--content {
    //     display: flex;
    //     flex-direction: column;
    //     row-gap: var(--rowContent4);
    // }

    &--wrap {
        // flex-direction: row-reverse;
        @media screen and (max-width: 600px) {
            // flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &--lf {
        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &-wrap {
        }

        &-list {
            row-gap: var(--rowContent3);
        }

        &-item {
            .like_item--des .note-text.content {
                display: none;
            }

            @media screen and (max-width: 800px) {
                width: 100%;

                .like_item--wrap {
                    flex-direction: row;
                    column-gap: var(--columnContent0);
                }

                .like_item--top {
                    width: 40%;
                    flex-shrink: 0;
                }

                .like_item--dm {
                    position: static;
                    transform: unset;
                    padding: 0;
                }
            }

            @media screen and (max-width: 600px) {
                width: 50%;

                .like_item--wrap {
                    flex-direction: column;
                    column-gap: var(--columnContent0);
                }

                .like_item--top {
                    width: 100%;
                    flex-shrink: 0;
                }

                .like_item--dm {
                    position: absolute;
                    transform: translateX(-50%);
                    top: 0.6rem;
                    left: 50%;
                    padding: 0 0.6rem;
                }
            }

            @media screen and (max-width: 360px) {
                width: 100%;

                .like_item--img {
                    padding-top: calc(100% * 140 / 280);
                }
            }
        }
    }

    &--rt {
        @media screen and (max-width: 600px) {
            width: 100%;
        }
        &-wrap {
            .like_item--img {
                padding-top: calc(100% * 382 / 592);
            }

            .like_item--wrap {
                row-gap: var(--rowContent2);
            }

            .like_item--des .note-text.content {
                display: -webkit-box !important;
            }

            .like_item--des .note-text.title {
                @include fz-24;
            }

            @media screen and (max-width: 800px) {
                height: 100%;

                .like_item--wrap {
                    position: sticky;
                    top: calc(var(--size-hd) + 2rem);
                }
            }
        }
    }
}

.like {
    &_item {
        @media screen and (max-width: 1024px) {
            width: calc(100% / 2);
        }

        @media screen and (max-width: 320px) {
            width: 100% !important;
        }

        &--wrap {
            // overflow: hidden;

            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            transition: 0.15s cubic-bezier(0.39, 0.575, 0.565, 1);
            position: relative;

            @include hover {
                &::before {
                    opacity: 1;
                }
            }

            @keyframes likeHover {
                0% {
                    transform: translate(-50%, -50%) scale(1);
                }
                100% {
                    transform: translate(-50%, -50%) scale(1.1);
                }
            }

            @include hover {
                // transform: scale(1.03);

                .like_item--img {
                    &::after {
                        opacity: 1;
                    }

                    &::before {
                        opacity: 1;
                        transform: translate(-50%, -50%) scale(1);
                    }

                    img {
                        transform: translate(-50%, -50%) scale(1.1);
                    }
                }
            }
        }

        &--des {
            display: flex;
            flex-direction: column;
            row-gap: 0.8rem;

            @media screen and (max-width: 1200px) {
                row-gap: 0.6rem;
            }

            @media screen and (max-width: 800px) {
                row-gap: 0.4rem;
            }
            @media screen and (max-width: 600px) {
                row-gap: 0.2rem;
            }

            .note-text {
                &.title {
                    position: relative;
                    a {
                        display: block;
                        position: relative;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                }

                &.content {
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    display: none;
                }
            }
        }

        &--info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            column-gap: 1.2rem;

            @media screen and (max-width: 1200px) {
                column-gap: 1rem;
            }

            @media screen and (max-width: 800px) {
                column-gap: 0.8rem;
            }

            @media screen and (max-width: 600px) {
                column-gap: 0.6rem;
            }

            @media screen and (max-width: 460px) {
                column-gap: 0.4rem;
            }

            .box-info-author {
                .note-mn {
                    font-weight: 400 !important;
                }

                .note-sm {
                    font-weight: 400 !important;
                }
            }
        }

        &--top {
            position: relative;

            .box-dm {
                @media screen and (max-width: 1200px) {
                    top: 0.5rem;
                    left: 0.5rem;
                }
            }
        }

        &--bottom {
            display: flex;
            flex-direction: column;
            row-gap: 0.4rem;
            justify-content: center;
            // position: relative;
            // @media screen and (max-width: 1200px) {
            //     row-gap: 0.8rem;
            // }

            // @media screen and (max-width: 800px) {
            //     row-gap: 0.6rem;
            // }

            // @media screen and (max-width: 600px) {
            //     row-gap: 0.4rem;
            // }
        }

        &--img {
            padding-top: calc(100% * 164 / 280);
            width: 100%;
            position: relative;
            overflow: hidden;
            border-radius: var(--radius16);
            display: block;
            height: 100%;

            @media screen and (max-width: 400px) {
                padding-top: calc(100% * 200 / 280);
            }

            @media screen and (max-width: 320px) {
                padding-top: calc(100% * 140 / 280);
            }

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                width: 100%;
                height: 100%;
                background: rgba($color: #000000, $alpha: 0.3);
                opacity: 0;
                transition: all 0.3s linear;
                z-index: 1;
            }

            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                border-radius: calc(var(--radius) / 2);
                width: 92%;
                height: 85%;
                border: 0.1rem solid $color-white;
                opacity: 0;
                transition: all 0.3s linear;
                z-index: 2;
                display: none;
            }

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 0.5s ease;
            }
        }

        @media screen and (max-width: 800px) {
            width: calc(100% / 3);
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2);
        }
    }
}

.post {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    overflow: hidden;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_filter {
        width: fit-content;
        margin: 0 auto;
        &--list {
            .swiper-slide {
                height: auto;
                width: fit-content;
            }
        }

        &--item {
            height: 100%;
            border-radius: var(--radius1);
            padding: 1rem;
            background: $color-gray;
            font-weight: 700;
            color: $color-text2;
            width: fit-content;
            margin: 0 auto;
            position: relative;
            @include fz-16;
            @include hover {
                color: $color-pri;
            }

            &.actived {
                padding: 1rem 1rem 1rem 4rem;
                color: $color-pri;
                &::before {
                    opacity: 1;
                }

                @media screen and (max-width: 1200px) {
                    padding: 1rem 1rem 1rem 3rem;
                }
            }

            &::before {
                content: "";
                position: absolute;
                width: 0.5rem;
                height: 0.5rem;
                border-radius: 50%;
                top: 50%;
                transform: translateY(-50%);
                left: 2rem;
                background: $color-pri;
                opacity: 0;
                transition: 0.3s linear;

                @media screen and (max-width: 1200px) {
                    left: 1.5rem;
                }
            }
        }
    }

    &_box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_list {
        row-gap: var(--rowContent3);
    }

    &_item {
        .like_item--img {
            padding-top: calc(100% * 288 / 388);
        }

        @media screen and (max-width: 800px) {
            width: calc(100% / 3);
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2);
        }

        @media screen and (max-width: 360px) {
            width: 100%;
        }
    }
}

.news_ct {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    position: relative;

    // .container {
    //     max-width: 168rem;
    //     padding: 0 1.6rem;
    // }

    &--box {
        @media screen and (max-width: 1200px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent3);
           
        }
    }

    &--op {
        display: flex;
        flex-direction: column;
        position: sticky;
        top: var(--size-hd);
        @media screen and (max-width: 1200px) {
            position: static;
            flex-direction: row;
        }

        .control-1-list,
        .control-2-list {
            flex-direction: column;
            row-gap: var(--rowContent0);

            @media screen and (max-width: 1200px) {
                column-gap: var(--columnContent0);
                flex-direction: row;
            }
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--imgs {
        display: flex;
        column-gap: 3.2rem;
    }

    &--img {
        width: 100%;
        height: auto;
        border-radius: var(--radius);
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            display: block;
        }
    }

    &--note {
        padding: 0 1.6rem;
        border-left: 0.3rem solid $color-pri;
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;

        @media screen and (max-width: 1200px) {
            padding: 0 1.4rem;
            row-gap: 0.6rem;
        }

        @media screen and (max-width: 800px) {
            padding: 0 1.2rem;
            row-gap: 0.4rem;
        }

        @media screen and (max-width: 600px) {
            padding: 0 1rem;
            row-gap: 0.2rem;
        }

        .note-sm {
            color: $color-text2;
        }

        &.cus {
            padding: 1.6rem;
            background: $color-gray5;
            border-radius: var(--radius);
            @media screen and (max-width: 1200px) {
                padding: 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem;
            }

            .note-sm {
                color: $color-text;
            }
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);

        &.mona-content {
            row-gap: unset;
            p {
                @include fz-18;
                @media screen and (max-width: 600px) {
                    font-size: 1.7rem !important;
                }
            }
        }

        .wp-caption {
            width: 100% !important;
        }

        img {
            margin: 0 auto;
            display: block;
        }

        // .artad-vid{
        //     img{
        //         margin: auto;
        //     }
        // }
    }

    &--title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);

        .like_item--dm {
            position: static;
            transform: unset;
            padding: 0;
        }
    }

    &--lf {
        width: calc(100% * 1 / 12);

        // @media screen and (max-width: 1600px) {
        //     width: calc(100% * 1.5 / 12);
        // }

        @media screen and (max-width: 1200px) {
            width: 100%;
        }

        &-wrap {
            position: relative;
            height: 100%;
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);
            height: 100%;

            .news_ct--op {
                width: fit-content;
                margin-left: auto;
                padding-top: 0vh;

                @media screen and (max-width:1200px) {
                    margin-left: unset;
                }

                @media screen and (max-width:460px) {
                    margin: 0 auto;
                }
            }
        }
    }

    &--ct {
        width: 100%;
        flex: 1;

        // @media screen and (max-width: 1200px) {
        //     width: calc(100% * 7.5 / 12);
        // }

        // @media screen and (max-width: 900px) {
        //     width: calc(100% * 7 / 12);
        // }

        // @media screen and (max-width: 800px) {
        //     width: 100%;
        // }

        // @media screen and (max-width: 600px) {
        //     width: 100%;
        // }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            &-content {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
                position: relative;

                img {
                    width: 100%;
                    border-radius: var(--radius3);
                    height: calc(100vw * 520 / 1300);
                }

                iframe {
                    width: 100%;
                    border-radius: var(--radius3);

                    height: calc(100vw * 520 / 1300);
                }

                @media screen and (max-width: 1200px) {
                    img {
                        width: 100%;

                        height: calc(100vw * 520 / 1200);
                    }

                    iframe {
                        width: 100%;

                        height: calc(100vw * 520 / 1200);
                    }
                }

                @media screen and (max-width: 800px) {
                    img {
                        width: 100%;

                        height: calc(100vw * 520 / 800);
                    }

                    iframe {
                        width: 100%;

                        height: calc(100vw * 520 / 800);
                    }
                }

                @media screen and (max-width: 360px) {
                    img {
                        width: 100%;
                        height: calc(100vw * 520 / 360);
                    }

                    iframe {
                        width: 100%;
                        height: calc(100vw * 520 / 360);
                    }
                }
            }
        }
    }

    &--utility {
        // padding-top: var(--rowContent2);
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
        // border-top: 0.1rem solid $color-gray4;
    }

    &--control {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);

        &-wrap {
            display: flex;
            align-items: center;
            column-gap: 1.2rem;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 1.2rem;
        }
    }

    &--keys {
        &-wrap {
            display: flex;
            column-gap: var(--columnContent2);
            
            // @media screen and (max-width:1200px) {
            //     flex-wrap: wrap;
            //     row-gap: var(--rowContent1);
            // }
            // flex-direction: column;
            // row-gap: var(--rowContent1);
        }

        &-list {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent1);
            row-gap: 0.8rem;
            flex-wrap: wrap;
        }
    }

    &--maybe {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &-item {
            padding-left: 2rem;
            position: relative;
            // line-height: 100%;

            &::before {
                content: "";
                position: absolute;
                top: 0.5rem;
                left: 0;
                width: 1.2rem;
                height: 1.2rem;
                border-radius: 50%;
                overflow: hidden;
                background-image: url(../assets/images/dot.svg);
                background-size: 100% 100%;
                background-repeat: no-repeat;
            }
        }

        &-link {
            font-weight: 600;
            color: $color-black-2;
            @include fz-16;
        }
    }
}

.post_more {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
}
