.ser_des {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: var(--rowContent3);
        }
    }

    &--lf {
        &-wrap {
            padding-top: var(--size-pd);
            padding-bottom: var(--size-pd);
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            row-gap: var(--rowContent2);
            max-width: 51.1rem;
            width: 100%;

            @media screen and (max-width: 1200px) {
                max-width: none;
            }

            @media screen and (max-width: 600px) {
                padding: 0;
            }

            @media screen and (max-width:460px) {
                .btn{
                    margin: 0 auto;
                }
            }
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent12);

            @media screen and (max-width:460px) {
                .title-md{
                    text-align: center;
                }

                
            }
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &--rt {
        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &-wrap {
            height: 100%;
        }

        &-img {
            padding-top: calc(100% * 380 / 592);
            width: 100%;
            position: relative;
            height: 100%;
            overflow: hidden;
            border-radius: var(--radius3);

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.ser_ct {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
    }

    &--item {
        border-radius: var(--radius3);
        overflow: hidden;

        &:nth-child(3n + 1) {
            background: $color-four;
        }

        &:nth-child(3n + 2) {
            background: $color-five;
        }

        &:nth-child(3n + 3) {
            background: $color-six;
        }

        &:nth-child(even) {
            .ser_ct--item-wrap {
                flex-direction: row-reverse;
            }
        }

        &-wrap {
            display: flex;

            .btn {
                margin: unset !important;
            }

            @media screen and (max-width: 460px) {
                flex-direction: column !important;
                // row-gap: var(--rowContent2);
            }
        }

        &-lf {
            width: 50%;
            overflow: hidden;

            @media screen and (max-width: 460px) {
                width: 100%;
            }

            &-wrap {
                height: 100%;
            }

            &-img {
                width: 100%;
                padding-top: calc(100% * 444 / 624);
                position: relative;
                height: 100%;
                overflow: hidden;
                display: block;

                img {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    object-fit: cover;
                }
            }
        }

        &-rt {
            width: 50%;
            @media screen and (max-width: 460px) {
                width: 100%;
            }
            &-wrap {
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 100%;
                row-gap: var(--rowContent3);
                padding: 4rem 6rem;

                @media screen and (max-width: 1200px) {
                    padding: 3rem 5rem;
                }

                @media screen and (max-width: 800px) {
                    padding: 2.6rem 3.2rem;
                }

                @media screen and (max-width: 600px) {
                    padding: 3.2rem 2.4rem;
                }

                @media screen and (max-width: 500px) {
                    padding: 3rem 2rem;
                }

                @media screen and (max-width: 460px) {
                    padding: 1.6rem;
                }
            }

            &-content {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);
            }
        }
    }
}

.ser_why {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        .swiper-slide {
            height: auto;

            @media screen and (max-width:360px) {
                width: 100%;
            }
        }
    }

    &--item {
        height: 100%;
        padding: 2.4rem;
        border-radius: var(--radius3);
        border: 0.1rem solid $color-gray;
        transition: 0.1s linear;
        @include hover {
            background: $color-hover;
            border-color: $color-hover;

            .ser_why--item-wrap {
                .ic {
                    img {
                        filter: brightness(0) saturate(100%) invert(3%) sepia(5%) saturate(1626%) hue-rotate(131deg)
                            brightness(99%) contrast(91%);
                    }
                }
            }

            .ser_why--item-box {
                .note-text {
                    color: $color-second;
                }
            }
        }

        &-wrap {
            height: 100%;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            .ic {
                width: 8rem;
                height: 8rem;
                display: block;
                margin: 0 auto;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                @media screen and (max-width: 1200px) {
                    width: 6rem;
                    height: 6rem;
                }

                @media screen and (max-width: 800px) {
                    width: 5rem;
                    height: 5rem;
                }

                @media screen and (max-width: 600px) {
                    width: 4rem;
                    height: 4rem;
                }
            }
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        @media screen and (max-width: 1200px) {
            padding: 2rem 1.2rem;
        }

        @media screen and (max-width: 800px) {
            padding: 1.8rem 1.2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.6rem 1.2rem;
        }
    }
}

.ser_about {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
}

.ser {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    overflow: hidden;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_list {
        position: relative;
        .box-navi {
            position: absolute;
            padding-top: calc(100% * 512 / 1200);
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-width: 40rem;
            z-index: 2;
            pointer-events: none;

            @media screen and (min-width: 2000px) {
                padding-top: calc(100% * 512 / 1350);
                max-width: 34rem;
            }

            @media screen and (max-width: 1200px) {
                max-width: 42%;
            }

            @media screen and (max-width: 800px) {
                max-width: 41%;
            }

            @media screen and (max-width: 600px) {
                max-width: 43%;
            }

            @media screen and (max-width: 500px) {
                max-width: 62%;
            }

            @media screen and (max-width: 360px) {
                max-width: 72%;
            }

            .btn-navi {
                border-radius: 50%;
                background: $color-white;
                position: absolute;
                top: 50%;
                z-index: 2;
                pointer-events: visible;
                border: none;
                box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.5);
                @include hover {
                    background: #ec1f26;
                    i {
                        color: $color-white;
                    }
                }

                @media screen and (max-width: 1200px) {
                    background: #ec1f26;
                    i {
                        color: $color-white !important;
                    }
                }

                transform: translateY(-50%);
                i {
                    color: $color-black;
                }

                &.prev {
                    left: -2.4rem;
                }

                &.next {
                    right: -2.4rem;
                }
            }
        }

        .swiper {
            overflow: visible;
        }

        .swiper-slide {
            height: auto;
        }

        .swiper-slide-active ~ .swiper-slide-next {
            transform: translate3d(-26%, 0px, 0px) rotateX(0deg) rotateY(-70deg) scale(0.95, 0.8) !important;
            .ser_item--box {
                left: 85% !important;
                transform: translate(-50%, 50%) rotate(90deg) !important;
                right: unset;

                @media screen and (max-width: 1200px) {
                    left: 90% !important;
                }

                @media screen and (max-width: 600px) {
                    left: 85% !important;
                }
            }

            @media screen and (min-width: 2000px) {
                transform: translate3d(-20%, 0px, 0px) rotateX(0deg) rotateY(-71deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 1200px) {
                opacity: 1;
                transform: translate3d(-22%, 0px, 0px) rotateX(0deg) rotateY(-70deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 800px) {
                opacity: 1;
                transform: translate3d(-22%, 0px, 0px) rotateX(0deg) rotateY(-65deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 600px) {
                opacity: 1;
                transform: translate3d(-24%, 0px, 0px) rotateX(0deg) rotateY(-65deg) scale(0.95, 0.8) !important;
            }
        }

        .swiper-slide-next + .swiper-slide {
            transform: translate3d(-68%, 0px, 0px) rotateX(0deg) rotateY(-80deg) scale(0.95, 0.85) !important;
            .ser_item--box {
                left: 85% !important;
                transform: translate(-50%, 50%) rotate(90deg) !important;
                right: unset;

                @media screen and (max-width: 1200px) {
                    left: 90% !important;
                }
            }

            @media screen and (min-width: 2000px) {
                transform: translate3d(-50.8%, 0px, 0px) rotateX(0deg) rotateY(-90deg) scale(0.95, 0.85) !important;
            }

            @media screen and (max-width: 1200px) {
                opacity: 0;
            }
        }

        .swiper-slide-next + .swiper-slide + .swiper-slide {
            transform: translate3d(-106%, 0px, 0px) rotateX(0deg) rotateY(-90deg) scale(0.95, 0.9) !important;
            .ser_item--box {
                left: 85% !important;
                transform: translate(-50%, 50%) rotate(90deg) !important;
                right: unset;

                @media screen and (max-width: 1200px) {
                    left: 90% !important;
                }
            }

            @media screen and (min-width: 2000px) {
                transform: translate3d(-77%, 0px, 0px) rotateX(0deg) rotateY(-105deg) scale(0.95, 0.9) !important;
            }

            @media screen and (max-width: 1200px) {
                @media screen and (max-width: 1200px) {
                    opacity: 0;
                }
            }
        }

        .swiper-slide + .swiper-slide-prev {
            transform: translate3d(26%, 0px, 0px) rotateX(0deg) rotateY(70deg) scale(0.95, 0.8) !important;
            .ser_item--box {
                right: 85% !important;
                transform: translate(50%, 50%) rotate(90deg) !important;
                left: unset;

                @media screen and (max-width: 1200px) {
                    right: 90% !important;
                }

                @media screen and (max-width: 600px) {
                    right: 85% !important;
                }
            }

            @media screen and (min-width: 2000px) {
                transform: translate3d(20%, 0px, 0px) rotateX(0deg) rotateY(71deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 1200px) {
                opacity: 1;
                transform: translate3d(22%, 0px, 0px) rotateX(0deg) rotateY(70deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 800px) {
                opacity: 1;
                transform: translate3d(22%, 0px, 0px) rotateX(0deg) rotateY(65deg) scale(0.95, 0.8) !important;
            }

            @media screen and (max-width: 600px) {
                opacity: 1;
                transform: translate3d(24%, 0px, 0px) rotateX(0deg) rotateY(65deg) scale(0.95, 0.8) !important;
            }
        }

        .swiper-slide {
            transform: translate3d(68%, 0px, 0px) rotateX(0deg) rotateY(80deg) scale(0.95, 0.85) !important;
            @media screen and (min-width: 2000px) {
                transform: translate3d(50.8%, 0px, 0px) rotateX(0deg) rotateY(90deg) scale(0.95, 0.85) !important;
            }

            @media screen and (max-width: 1200px) {
                opacity: 0;
            }
            .ser_item--box {
                right: 85%;
                transform: translate(50%, 50%) rotate(90deg);
                left: unset;

                @media screen and (max-width: 1200px) {
                    right: 90% !important;
                }
            }
            &.slide-custom {
                transform: translate3d(106%, 0px, 0px) rotateX(0deg) rotateY(090deg) scale(0.95, 0.9) !important;
                .ser_item--box {
                    right: 85% !important;
                    transform: translate(50%, 50%) rotate(90deg);
                    left: unset;

                    @media screen and (max-width: 1200px) {
                        right: 90% !important;
                    }
                }

                @media screen and (min-width: 2000px) {
                    transform: translate3d(77%, 0px, 0px) rotateX(0deg) rotateY(105deg) scale(0.95, 0.9) !important;
                }

                @media screen and (max-width: 1200px) {
                    opacity: 0;
                }
            }

            &.slide-custom2 {
                opacity: 0;
            }
        }

        .swiper-slide-active {
            transform: scale(1) !important;
            .ser_item--box {
                bottom: 4rem !important;
                right: unset !important;
                left: 0% !important;
                transform: translate(0%, 0%) rotate(0deg) !important;
                opacity: 0;
                transition: unset;
                animation: anHien 0.5s 0.5s linear forwards;
                .note-text {
                    display: -webkit-box;
                    animation: anHien 0.5s 0s linear forwards;
                }

                @media screen and (max-width: 800px) {
                    bottom: 3rem !important;
                }

                @media screen and (max-width: 600px) {
                    bottom: 2rem !important;
                }
            }

            .ser_item--img {
                &::before {
                    opacity: 0;
                }

                &::after {
                    opacity: 1;
                }
            }

            @media screen and (max-width: 1200px) {
                opacity: 1;
            }
        }
    }

    &_item {
        height: 100%;
        display: block;

        &--wrap {
            height: 100%;
        }

        &--img {
            padding-top: calc(100% * 512 / 384);
            width: 100%;
            position: relative;
            overflow: hidden;
            display: block;
            border-radius: var(--radius3);
            box-shadow: 0px 9px 18px 0px #00000026;

            &::before {
                content: "";
                position: absolute;
                inset: 0;
                width: 100%;
                background: linear-gradient(0deg, rgba(31, 1, 1, 0.3), rgba(31, 1, 1, 0.3));
                transition: 0.5s linear;
                height: 100%;
                z-index: 1;
                border-radius: var(--radius3);
            }

            &::after {
                content: "";
                position: absolute;
                inset: 0;

                width: 100%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%);
                transition: 0.5s linear;
                height: 100%;
                z-index: 1;
                border-radius: var(--radius3);
                opacity: 0;
            }

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &--box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
            position: absolute;
            bottom: 50%;
            padding: 0 2.4rem;
            // transition: 0.3s linear;
            width: 100%;
            z-index: 2;
            // bottom: 4rem;
            // left: 0%;
            // transform:translate(0%,0%) rotate(0deg);

            @keyframes anHien {
                0% {
                    opacity: 0;
                }
                50% {
                    opacity: 0.5;
                }
                100% {
                    opacity: 1;
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 0 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0 1.2rem;
            }

            .note-lg {
                max-width: 80%;
                width: 100%;
                margin: 0 auto;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }

            .note-text {
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden;
                display: none;
                opacity: 0;
            }
        }
    }
}

.ser_brand {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    overflow: hidden;
    .box-title {
        .note-text {
            margin: 0 auto;
            max-width: 55rem;
            width: 100%;
        }
    }

    &--all {
        // margin-left: 4rem;
        // margin-right: 4rem;
        // overflow: hidden;
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        .swiper {
            overflow: visible;

            @media screen and (max-width: 1200px) {
                overflow: hidden;
            }
        }

        .swiper-slide {
            height: auto;
            width: calc(100% / 6);

            @media screen and (max-width: 1200px) {
                width: calc(100% / 6);
            }

            @media screen and (max-width: 1024px) {
                width: calc(100% / 5);
            }

            @media screen and (max-width: 800px) {
                width: calc(100% / 4);
            }

            @media screen and (max-width: 600px) {
                width: calc(100% / 3);
            }

            @media screen and (max-width: 460px) {
                width: calc(100% / 3);
            }

            @media screen and (max-width: 360px) {
                width: calc(100% / 2);
            }
        }
    }

    &--item {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 3.2rem;
        background: $color-gray;
        border-radius: var(--radius3);

        @media screen and (max-width: 1400px) {
            padding: 2rem;
        }

        @media screen and (max-width: 1200px) {
            padding: 2.4rem;
        }

        @media screen and (max-width: 800px) {
            padding: 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.6rem;
        }

        @media screen and (max-width: 460px) {
            padding: 1.2rem;
        }

        @include hover {
            .ser_brand--img {
                img {
                    filter: unset;
                }
            }
        }
    }

    &--img {
        max-width: 90%;
        width: 100%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            filter: brightness(0) saturate(100%) invert(9%) sepia(60%) saturate(442%) hue-rotate(176deg) brightness(91%)
                contrast(88%);

                @media screen and (max-width:1200px) {
                    filter: unset;
                }
        }
    }
}
