.home_bn {
    overflow: hidden;
    margin-bottom: var(--size-pd);
    height: calc(100vh - var(--size-hd));

    // @media screen and (max-width: 1200px) {
    //     height: calc(100vw * 766 / 1728);
    // }

    // @media screen and (max-width: 460px) {
    //     height: calc(100vw * 766 / 1024);
    // }

    &--wrap {
        height: 100%;
    }

    &--bg {
        width: 100%;
        height: 100%;
        @media screen and (min-width: 1200px) {
            animation: thuNho 0.8s linear;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.home_make {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
    &--list {
        display: flex;
        justify-content: space-between;
        column-gap: var(--columnContent2);
        row-gap: var(--columnContent2);
        flex-wrap: wrap;
    }

    &--item {
        width: 20%;

        @media screen and (max-width: 1200px) {
            width: 25%;
        }

        @media screen and (max-width: 600px) {
            width: calc(50% - (var(--columnContent2)));
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            .title-mn {
                text-align: center;
            }
        }

        &-ic {
            max-width: 31rem;
            width: 100%;
            height: auto;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            // align-items: center;

            @media screen and (max-width: 1200px) {
                max-width: 25rem;
            }
            img {
                width: auto;
                height: auto;
                object-fit: cover;
            }
        }

        &:nth-child(3n + 2) {
            width: 40%;

            @media screen and (max-width: 600px) {
                width: 100%;

                order: -1;
            }
        }
    }
}

.home_ex {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;

    &--wrap {
        display: flex;
        flex-direction: column;
    }

    &--top {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        padding-bottom: var(--size-pd);
    }

    &--center {
        padding-top: var(--size-pd);
        padding-bottom: var(--size-pd);

        .about_our--rt-list{
            @media screen and (max-width:360px) {
                flex-direction: column;
                row-gap: var(--rowContent0);
            }
        }

        .about_our--rt-item {
            border: none;
            border-radius: 0;

            .note-text {
                @include fz-40;
                font-weight: 700;
            }

            @media screen and (max-width:360px) {
                width: 100%;
            }
        }

        .about_our--rt-num {
            display: flex;
            column-gap: var(--columnContent0);
            justify-content: center;
            align-items: center;

            .num {
                font-size: 15rem;
                line-height: 1;

                @media screen and (max-width: 1200px) {
                    @include fz-120;
                }
            }

            .plus {
                font-size: 15rem;
                top: -4rem;
                right: -10rem;

                @media screen and (max-width: 1200px) {
                    @include fz-120;
                right: -8rem;
                    top: -3.5rem;
                }

                @media screen and (max-width: 800px) {
                    right: -6rem;
                    top: -3rem;
                }

                @media screen and (max-width: 600px) {
                    right: -5rem;
                    top: -2rem;
                }

                @media screen and (max-width: 500px) {
                    right: -4rem;
                    top: -2rem;
                }

                @media screen and (max-width: 460px) {
                    right: -3.5rem;
                    top: -2rem;
                }
            }
        }
    }

    &--bottom {
        padding-top: var(--size-pd);

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            .btn {
                margin: 0 auto;
            }
        }
    }
}

.home_across {
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);
    background: #f3ffff;
    position: relative;
    overflow: hidden;

    @media screen and (max-width: 1200px) {
        margin-top: 0;
    }
    &--wrap {
        flex-wrap: nowrap;

        @media screen and (max-width: 1200px) {
            flex-direction: column;
            row-gap: var(--rowContent1);
        }
    }

    // &.ani{
    //     &.showed{
    //         .home_across--rt{
    //             transform: scale(1);
    //         opacity: 1;
    //         }
    //     }
    // }

    &--lf {
        width: calc(100% * 4 / 12);

        @media screen and (max-width: 1200px) {
            width: 100%;
        }

        &-wrap {
            padding-top: var(--size-pd);
            padding-bottom: var(--size-pd);
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);
            justify-content: center;
            height: 100%;

            .btn {
                @media screen and (max-width: 1200px) {
                    margin: 0 auto;
                }
            }
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            .title-cus {
                margin: unset;

                @media screen and (max-width: 1200px) {
                    margin: 0 auto;
                }
            }
            .title-lg {
                text-align: start;

                @media screen and (max-width: 1200px) {
                    text-align: center;
                }
            }

            .note-text {
                @media screen and (max-width: 1200px) {
                    text-align: center;
                }
            }
        }
    }

    &--rt {
        // width: 100%;
        flex: 1;
        position: relative;
        // @media screen and (min-width:1200px) {
        //     transform: scale(1.1);
        //     opacity: 0;
        //     transition: 1s linear;
        // }

        &-wrap {
            position: relative;
            margin-right: calc((100vw - 120rem) / -2);

            @media screen and (max-width: 1230px) {
                margin-right: -1.5rem;
            }
            @media screen and (max-width: 1200px) {
                margin-left: -1.5rem;
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent3);
            }
        }

        &-map {
            padding-top: calc(100% * 712 / 1043);
            position: relative;
            display: block;
            width: 100%;
            cursor: url("../assets/images/Location.svg"), auto;
            svg {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
                path {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    transition: 0.3s linear;

                    @include hover {
                        fill: #c3f7f6 !important;
                    }

                    
                    &:nth-child(13){
                        pointer-events: none;
                    }

                    &:nth-child(22) {
                        pointer-events: none;
                    }

                    &:nth-child(23) {
                        pointer-events: none;
                    }

                    &:nth-child(24) {
                        pointer-events: none;
                    }

                    &:nth-child(25) {
                        pointer-events: none;
                    }

                    &:nth-child(26) {
                        pointer-events: none;
                    }

                    &:nth-child(27) {
                        pointer-events: none;
                    }

                    &:nth-child(28) {
                        pointer-events: none;
                    }

                    &:nth-child(29) {
                        pointer-events: none;
                    }

                    &:nth-child(30) {
                        pointer-events: none;
                    }

                    &:nth-child(31) {
                        pointer-events: none;
                    }

                    &:nth-child(32) {
                        pointer-events: none;
                    }

                    &:nth-child(33) {
                        pointer-events: none;
                    }

                    &:nth-child(34) {
                        pointer-events: none;
                    }

                    &:nth-child(35) {
                        pointer-events: none;
                    }
                    &:nth-child(36) {
                        pointer-events: none;
                    }

                    &:nth-child(37) {
                        pointer-events: none;
                    }

                    &:nth-child(38) {
                        // pointer-events: none;
                    }

                    &.actived {
                        fill: #c3f7f6 !important;
                    }
                }
            }
        }

        &-ex {
            position: absolute;
            top: 2rem;
            right: 2rem;
            width: 2rem;
            height: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 2;
            cursor: pointer;
            i {
                @include fz-24;
                color: $color-second;
            }

            @media screen and (max-width: 1200px) {
                display: none;
            }
        }

        &-list {
            position: absolute;
            bottom: 2%;
            right: 2%;
            z-index: 1000;
            max-width: 70%;
            width: 100%;
            height: auto;

            @media screen and (max-width: 1400px) {
                max-width: 85%;
            }

            @media screen and (max-width: 1200px) {
                position: relative;
                bottom: unset;
                right: unset;
                max-width: unset;
            }

            &-wrap {
                position: relative;
            }
        }

        &-item {
            display: none;

            &.showed {
                display: block;
                @keyframes anHien2 {
                    0% {
                        transform: scale(0);
                        opacity: 0;
                    }
                    100% {
                        transform: scale(1);
                        opacity: 1;
                    }
                }

                @media screen and (max-width: 1200px) {
                    @keyframes anHien2 {
                        0% {
                            opacity: 0;
                        }
                        100% {
                            opacity: 1;
                        }
                    }
                }
                animation: anHien2 0.6s linear;
            }
            &-wrap {
                min-height: 32rem;
                padding: 2.4rem;
                overflow: hidden;
                background: $color-white;
                display: flex;
                column-gap: var(--columnContent1);
                box-shadow: 0px 4px 23.4px 0px #0c0f1f0d;
                background: $color-white;
                border-radius: var(--radius3);
                overflow: hidden;
                position: relative;
                justify-content: space-between;

                @media screen and (max-width: 1200px) {
                    min-height: 20rem;
                    padding: 1.5rem;
                    box-shadow: unset;
                    border-radius: 0;
                    border-bottom: 0.1rem solid rgba($color: #000000, $alpha: 0.1);
                }

                @media screen and (max-width: 500px) {
                    flex-direction: column;
                }
            }

            &-not {
                display: flex;
                flex-direction: column;
                justify-content: center;
                // height: 100%;
                width: 100%;
            }

            &-imgs {
                flex: 1;
                width: 100%;
                // height: 100%;
                overflow: hidden;
                border-radius: var(--radius1);
                // flex-shrink: 0;
                .swiper {
                    height: 100%;
                }

                .swiper-slide {
                    height: auto;
                }
            }

            &-boxs {
                width: calc(50% - var(--columnContent1));
                padding: 2rem 0;
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
                // margin: 0 -1rem;

                .swiper {
                    height: 100%;
                }

                .swiper-slide {
                    height: auto;
                }

                .swiper-slide {
                    height: auto;
                }

                .box-navi {
                    margin-top: auto;

                    @media screen and (max-width: 460px) {
                        justify-content: center;
                    }
                }

                @media screen and (max-width: 600px) {
                    padding: 1.2rem 0;
                }

                @media screen and (max-width: 500px) {
                    width: 100%;
                }
            }

            &-img {
                padding-top: calc(100% * 278 / 384);
                width: 100%;
                height: 100%;
                overflow: hidden;
                display: block;
                position: relative;

                @media screen and (max-width: 1200px) {
                    padding-top: calc(100% * 260 / 384);
                }

                img {
                    position: absolute;
                    transform: translate(-50%, -50%);
                    top: 50%;
                    left: 50%;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &-box {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);
                padding: 0 1rem;

                @media screen and (max-width: 600px) {
                    padding: 0 0.5rem;
                }
            }

            &-title {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);
                .title-sm {
                    line-height: 1.2;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }

                .note-sm {
                    color: $color-text4;

                    @include hover {
                        color: $color-pri;
                    }
                }
            }

            &-content {
                .note-text {
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
            }

            &-link {
                .note-text {
                    text-decoration: underline;

                    @include hover {
                        color: $color-second;
                    }
                }
            }
        }
    }
}

