.popup_main {
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 74rem;
    width: 100%;
    height: auto;
    transform: translate(-50%, -50%);
    background: $color-second;
    z-index: 3000;
    padding: 4rem 3.2rem;
    border-radius: 1.6rem;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.5s linear;

    &.bg {
        background: $color-pri;
    }

    &.showed {
        opacity: 1;
        visibility: visible;
        pointer-events: visible;
        transform: translate(-50%, -50%) scale(1);

        & ~ .popup_main--modal {
            opacity: 1;
            visibility: visible;
            pointer-events: visible;
        }
    }
    .alert--wrap {
        display: flex;
        flex-direction: column;
        row-gap: 1.2rem;
        width: 100%;
        .title-md {
            text-align: center;
        }

        .btn {
            margin: 0 auto;
        }
    }

    &--wrap {
        display: flex;
        width: 100%;

        // column-gap: 1rem;
        // background: $color-white;

        .btn {
            margin: auto;
        }

        .about_dg--item {
            border-radius: 0.8rem;
            width: 100%;
            &-content {
                .note-lg {
                    font-style: normal;
                    color: $color-text2;
                }
            }

            &-box {
                flex-direction: row;
            }

            &-wrap {
                row-gap: 2.4rem;
                &::before {
                    display: none;
                }
            }
            @include hover {
                background: $color-white !important;

                .about_dg--info {
                    .note-text {
                        color: $color-text;
                    }
                }
            }
        }

        .contact_form-list {
            overflow-x: hidden;
            max-height: 55vh;
            // padding-right: 0.1rem;
            overflow-y: auto;
        }
    }

    &--exits {
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        // background: $color-pri;

        position: absolute;
        top: 0rem;
        right: 0rem;
        cursor: pointer;
        transition: all 0.3s linear;
        @include hover {
            i {
                color: $color-pri;
                font-size: 2.4rem;
            }
        }
        i {
            color: $color-white;
            font-size: 2.4rem;
        }
    }

    @media screen and (max-width: 800px) {
        max-width: 90%;
        padding: 4rem 2.4rem;
    }

    @media screen and (max-width: 600px) {
        max-width: 90%;
        padding: 4rem 1.2rem 3.2rem;
    }
}

.popup_main--modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2999;
    background: rgba(24, 49, 90, 0.6);
    backdrop-filter: blur(1.5rem);

    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    transition: all 0.5s linear;
}


.form-upload {
    cursor: pointer;

    &.actived {
        .form-upload-wrap {
            border-color: $color-pri;
        }
    }
    &-wrap {
        padding: var(--rowContent3);
        border: 0.1rem dashed #334155;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        transition: 0.3s linear;
        border-radius: 0.8rem;
        .wpcf7-form-control-wrap {
            display: block;
            width: 100%;
            height: 100%;
        }

        .wpcf7-not-valid-tip {
            bottom: 0 !important;
            left: 50%;
            transform: translateX(-50%);
            font-size: 0.8rem !important;
        }

        input {
            display: none;
        }

       
    }

    &-title {
        color: $color-pri;
        text-align: center;

        .note-text {
            text-align: center;
            text-transform: uppercase;
        }
    }

    &-content {
        .note-sm {
            color: $color-text4;
            text-align: center;

            strong {
                font-weight: 700;
            }
        }
    }

    &-img {
        width: 4rem;
        height: 4rem;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;


        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.form-ip {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent12);

    @media screen and (max-width: 1200px) {
        row-gap: 0.6rem;
    }

    &.package-ip {
        pointer-events: none;
    }

  


    &-label {
        label {
            text-transform: uppercase;
            font-weight: 700;
            color: $color-second;
            @include fz-16;
            strong {
                color: #cf0011;
            }
        }
    }

    &-ip {
        position: relative;
        input {
            width: 100%;
            height: 5rem;
            padding: 1rem 1.6rem;
            border-radius: var(--radius0);
            border: 0.1rem solid $color-gray2;
            background: $color-white;
            color: $color-text;
            transition: all 0.3s linear;
            @include fz-16;
            @media screen and (max-width: 1200px) {
                height: 4.2rem;
                padding: 1rem 1.2rem;
            }

            @media screen and (max-width: 800px) {
                height: 4rem;
                padding: 1rem 1rem;
            }

            @media screen and (max-width: 600px) {
                height: 3.6rem;
                padding: 1rem 1rem;
            }
            &::placeholder {
                @include fz-16;
                color: $color-text4;

                @media screen and (max-width: 460px) {
                    @include fz-12;
                }
            }

            &:focus {
                border-color: $color-second;
            }
        }

        textarea {
            width: 100%;
            min-height: 14rem;
            padding: 1rem 1.6rem;
            border-radius: var(--radius0);

            border: 0.1rem solid $color-gray4;
            background: $color-white;
            transition: all 0.3s linear;
            color: $color-text;
            @include fz-16;
            &::placeholder {
                @include fz-16;
                color: $color-text4;


                @media screen and (max-width: 460px) {
                    @include fz-12;
                }
            }

            &:focus {
                border-color: $color-second;
            }

            @media screen and (max-width: 1200px) {
               
                padding: 1rem 1.2rem;
            }

            @media screen and (max-width: 800px) {
                height: 14rem;
                padding: 1rem 1rem;
            }

            @media screen and (max-width: 600px) {
                
                padding: 1rem 1rem;
            }

            
        }

       
    }

    &-ic {
        display: flex;
        cursor: pointer;
        align-items: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 1.6rem;

        @include hover {
            i {
                color: $color-pri;
            }
        }

        i {
            @include fz-16;
            font-weight: 300 !important;
            color: #b1b1b1;
            transition: all 0.3s linear;
        }

        @keyframes anHienPass {
            0% {
                opacity: 0;
            }

            50% {
                opacity: 0.5;
            }

            100% {
                opacity: 1;
            }
        }

        &.hidden-pass {
            animation: anHienPass 0.5s ease forwards;
        }

        &.show-pass {
            display: none;

            animation: anHienPass 0.5s ease forwards;
        }
    }
}

.form-ck{
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent1);

    &-list{
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);
    }

    &-item{
        display: flex;
        column-gap: 1rem;
        cursor: pointer;
        

        input{
            display: none;

            &:checked{
                & ~ .box{
                    background: $color-pri;
                    border-color: $color-pri;
                    &::before{
                        opacity: 1;
                    }
                }
            }
        }

        .box{
            flex-shrink: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2rem;
            height: 2rem;
            border-radius: 0.4rem;
            border: 0.1rem solid #A1ABAB;
            transition: 0.3s linear;
            background: transparent;
            position: relative;

            &::before{
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                background-image: url(../assets/images/check.svg);
                width: 1rem;
                height: 1rem;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                transform: translate(-50%,-50%);
                opacity: 0;
                transition: 0.3s linear;
            }

        }
    }
}

.contact {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    overflow: hidden;
    &_wrap {
        padding-top: var(--size-pdl);
        padding-bottom: var(--size-pdl);
        background: #edf2f2;
        border-radius: var(--radius3);

        @media screen and (max-width: 1200px) {
            padding-top: var(--size-pd);
        padding-bottom: var(--size-pd);


        }
        
        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: 1.6rem;
        }
    }

    &_form{
        &--list{
            row-gap: var(--rowContent2);
        }

        &--wrap{
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent6);

            .btn{
                margin: 0 auto;
            }
        }

        &--item{
            width: 100%;


            &:nth-child(1){
                width: 50%;
            }       
            &:nth-child(2){
                width: 50%;
                
            }
            &:nth-child(3){
                width: 50%;
                
            }
            &:nth-child(4){
                width: 50%;
                
            }
            &:nth-child(5){
                width: 50%;
                
            }
            &:nth-child(6){
                width: 50%;
                
            }

            @media screen and (max-width:460px) {
                width: 100% !important;
            }
        }
    }

    &_box{
        max-width: 80rem;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent6);
        padding:0 1.2rem;
    }

    &_title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        



        .social-mb-list{
            justify-content: center;
        }

        .social-mb-link{
            width: 3.2rem;
            height: 3.2rem;
        }
    }
}
