.page-numbers {
  @include flex;
  align-items: center;
  justify-content: center;

  &.m-top {
    margin-top: 3rem;
  }

  .dot {
    width: 5.3rem;
    height: 0.1rem;
    // background: $color-text;
  }

  > li {
    &:not(:first-child) {
      margin-left: 0.8rem;

      @include max(460px) {
        margin-left: 0.6rem;
      }
    }

    & > .page-numbers {
      line-height: 1.5;
      font-weight: 400;
      color: #A1ABAB;
      @include mid-flex;
      cursor: pointer;
      text-align: center;
      transition: 0.3s ease-in-out;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 0.3rem;
      // border: 0.1rem solid $color-gray;
      @include fz-16;
      font-family: var(--font-pri);

      @include hover {
        // background-color: $color-pri;
        color: $color-white;
        background: $color-pri;
        // border-color: $color-pri;

        i {
          color: $color-white;
        }
      }

      @include max(800px) {
      
        font-size: 1.4rem;
      }

      // &:hover {
      //   @include min(1201px) {}
      // }

      &.current {
        background: $color-pri;
        color: $color-white;
      }

      &.dot {
        pointer-events: none;
      }

      .icon {
        font-weight: 400;
        color: #A1ABAB;
      }
    }
  }

  .page-numbers.next,
  .page-numbers.prev {
    cursor: pointer;

    i {
      @include fz-16;
      font-weight: 400;
      color: #A1ABAB;;
    }

    > .page-numbers {
    }

    @include hover {
      color: $color-text2;

      background: $color-pri;
     
    }

    .icon {
      font-weight: 400 !important;

      cursor: pointer;
      transition: all 0.3s ease-in-out;
    }
  }
}

.pagination {
  &.center {
    .page-numbers {
      justify-content: center;
    }
  }
}
