.footer {
  padding-top: 10rem;
  padding-bottom: 10rem;
  background: $color-second;
  position: relative;
  overflow: hidden;
  z-index: 1;
  position: relative;
  z-index: 1000;

  // border-top: 0.1rem solid $color-four;
  @media screen and (max-width: 1200px) {
    padding-top: 4rem;
    padding-bottom: 2rem;
  }

  @media screen and (max-width: 600px) {
    padding-top: 3rem;
    padding-bottom: 1.2rem;
  }

  &-logo {
    max-width: 20rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (max-width: 1024px) {
      max-width: 18rem;
    }

    @media screen and (max-width: 800px) {
      max-width: 16rem;
    }

    @media screen and (max-width: 600px) {
      max-width: 14rem;
    }
  }

  &-info {
    // display: flex;
    // flex-direction: column;
    // row-gap: 1.6rem;
    display: inline;

    &-list {
      display: flex;
      flex-direction: column;
      row-gap: 1.2rem;

      @media screen and (max-width: 1024px) {
        row-gap: 1rem;
      }

      @media screen and (max-width: 800px) {
        row-gap: 0.8rem;
      }
    }

    &-wrap {
      // display: flex;
      // flex-direction: column;
      // width: 100%;
      // flex: 1;
      display: inline;
    }

    &-item {
      display: inline;

      .note-text {
        // min-width: 5rem;
        display: inline-block;
      }

      @media screen and (max-width: 1024px) {
        column-gap: 1.2rem;
      }

      width: 100%;

      .note-sm {}
    }

    &-link {
      width: fit-content;
      @include fz-16;
      color: $color-gray;
      flex-shrink: 0;

      @include hover {
        color: $color-pri;
      }
    }

    &-ic {
      width: 2rem;
      height: 2rem;
      flex-shrink: 0;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        // filter: brightness(0) saturate(100%) invert(30%) sepia(15%) saturate(824%) hue-rotate(176deg) brightness(97%)
        //   contrast(89%);
      }
    }
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    margin: 0;
    row-gap: 0.8rem;
    height: 100%;

    @media screen and (max-width: 1024px) {
      flex-direction: row;
      column-gap: 1.2rem;
      row-gap: 1.2rem;
      flex-wrap: wrap;
    }

    @media screen and (max-width: 460px) {
      flex-direction: column;
      column-gap: 0.8rem;
      row-gap: 1rem;
      flex-wrap: wrap;
    }

    @media screen and (max-width: 330px) {
      flex-direction: row;
      column-gap: 0.6rem;
      row-gap: 1.4rem;
      flex-wrap: wrap;
    }

    .menu-item {
      height: auto;

      &.current-menu-item {
        .menu-link {
          color: $color-pri;
          font-weight: 700;
        }
      }

      .menu-link {
        padding: 0;
        color: $color-gray2;
        font-weight: 500;
        // height: 0;
        @include fz-14;
        color: $color-gray4;

        &::before {
          display: none;
        }

        @include hover {
          color: $color-pri;
        }
      }
    }
  }

  &-wrap {
    flex-direction: column;
    row-gap: var(--rowContent3);
    display: flex;
  }

  &-box {}

  &-item {
    position: relative;
    z-index: 3;

    .social-mb-list {
      column-gap: var(--columnContent3);
      padding: 0;
      margin: 0;
      justify-content: center;
    }

    .social-mb-link {
      width: 5.6rem;
      height: 5.6rem;

      @include hover {
        transform: scale(1.2);
      }

      @media screen and (max-width:1200px) {
        width: 4.2rem;
        height: 4.2rem;
      }

      @media screen and (max-width:800px) {
        width: 3.6rem;
        height: 3.6rem;
      }

      @media screen and (max-width:600px) {
        width: 3.2rem;
        height: 3.2rem;
      }
    }

    &:first-child {
      .footer-item-content {
        width: 100%;
      }
    }

    &:nth-child(2) {
      .footer-item-content {
        max-width: 32rem;
        margin: 0 auto;
        width: 100%;
        row-gap: var(--rowContent2);
      }
    }

    &:last-child {
      .footer-item-content {
        width: 100%;
      }
    }

    @media screen and (max-width: 1024px) {
      width: calc(100% / 2) !important;

      &:first-child {
        order: 2;


      }

      &:nth-child(2) {
        order: -1;

        width: 100% !important;

      }

      &:last-child {
        order: 2;
      }


    }

    @media screen and (max-width: 460px) {
      width: 100% !important;
    }

    &-content {
      width: fit-content;
      height: 100%;
      display: flex;
      flex-direction: column;
      row-gap: var(--rowContent1);

      @media screen and (max-width: 1024px) {
        width: 100%;
      }

      @media screen and (max-width: 800px) {}

      @media screen and (max-width: 600px) {}

      .note-lg {
        text-transform: uppercase;
        font-family: "Montserrat";

        @media screen and (max-width: 1024px) {
          font-size: 1.8rem;
        }
      }
    }

    &-wrap {
      display: flex;
      justify-content: center;

      @media screen and (max-width: 1024px) {
        justify-content: start;
      }
    }

    &:last-child {
      .footer-item-wrap {
        justify-content: flex-end;

        @media screen and (max-width: 1024px) {
          justify-content: start;
        }
      }
    }
  }

  &-list {
    @media screen and (max-width: 1024px) {
      row-gap: 2.4rem;
    }

    @media screen and (max-width: 600px) {
      row-gap: 2rem;
    }
  }

  &-mona {
    display: flex;
    align-items: center;
    column-gap: 0.6rem;
    justify-content: space-between;
    width: 100%;
    padding-top: 3.2rem;
    border-top: 0.1rem solid rgba($color: $color-text3, $alpha: 0.2);

    @media screen and (max-width: 1200px) {
      padding-top: 2rem;

    }

    @media screen and (max-width: 600px) {
      padding-top: 1.2rem;

    }



    &-wrap {
      display: flex;
      align-items: center;
      column-gap: 0.6rem;

      @media screen and (max-width: 1024px) {
        width: 100%;
        justify-content: center;
      }
    }

    &-img {
      max-width: 18rem;
      display: flex;
      align-items: center;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  &-ip {
    display: flex;
    height: 4.4rem;
    background: $color-white;
    border-radius: var(--radius1);
    position: relative;

    @media screen and (max-width: 1024px) {
      height: 4rem;
    }

    @media screen and (max-width: 600px) {
      height: 3.8rem;
    }

    input {
      border: none;
      width: 100%;
      display: flex;
      align-items: center;
      padding: 0rem 4.5rem 0rem 1.2rem;
      color: $color-black;
      height: 100%;
      @include fz-14;

      &::placeholder {
        color: $color-text4;
        @include fz-14;
      }
    }

    .btn {
      width: fit-content;
      min-width: unset;
      padding: 1.2rem;
      border-radius: 0rem;
      height: 85%;
      position: absolute;
      right: 0.3rem;
      top: 50%;
      transform: translateY(-50%);
      background: transparent;

      @include hover{
        background: unset !important;;
      }
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      row-gap: 1.2rem;

      // @media screen and (max-width: 1024px) {
      //   max-width: 80%;
      //   width: 100%;
      // }

      // @media screen and (max-width: 600px) {
      //   max-width: none;
      // }
    }
  }

  &-mxh {
    // @media screen and (max-width: 500px) {
    //   margin: 0 auto 2.4rem;
    // }

    &-list {
      display: flex;
      column-gap: 1rem;

      @media screen and (max-width: 1024px) {
        column-gap: 0.6rem;
      }
    }

    &-item {}

    &-link {
      width: 3.2rem;
      height: 3.2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      overflow: hidden;
      transition: all 0.3s ease;

      @media screen and (max-width: 1024px) {
        width: 2.4rem;
        height: 2.4rem;
      }

      @include hover {
        transform: scale(1.3);
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}