.banner-main {
    position: relative;
    margin-bottom: var(--size-pd);
    overflow: hidden;
    &--wrap {
        display: flex;
        align-items: center;
        height: 100%;
        min-height: calc(100vw * 350 / 1728);

        @media screen and (max-width: 1200px) {
        }
    }

    &--content {
        padding-top: 10rem;
        padding-bottom: 10rem;
        display: flex;
        flex-direction: column;
        row-gap: 1rem;

        @media screen and (max-width: 1200px) {
            padding-top: 8rem;
            padding-bottom: 8rem;
        }

        @media screen and (max-width: 600px) {
            padding-top: 6rem;
            padding-bottom: 6rem;
            row-gap: 0.5rem;
        }
    }

    @keyframes textChay {
        0% {
            clip-path: inset(0 100% 0 0);
        }

        100% {
            clip-path: inset(0 0 0 0);
        }
    }

    @keyframes hinhChay {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 0.5;
        }

        100% {
            opacity: 1;
        }
    }

    .breadcrumbs-list {
        @media screen and (min-width: 1200px) {
            opacity: 0;
            animation: hinhChay 0.8s linear forwards 1.4s;
        }
        .breadcrumbs-item a {
            color: $color-gray2;
        }

        .breadcrumbs-item:not(:last-child)::before {
            color: $color-gray2;
        }

        .breadcrumbs-item.current a {
            color: $color-white !important;
        }
    }

    .title-lg {
        font-weight: 700;
        text-transform: uppercase;
        color: $color-white;

        @media screen and (min-width: 1200px) {
            clip-path: inset(0 100% 0 0);
            animation: textChay 0.8s linear forwards 0.8s;
        }
    }

    // .note-lg {
    //     font-weight: 700;
    //     color: $color-second;
    //     font-family: "Roboto";
    //     margin-bottom: 3rem;
    //     clip-path: inset(0 100% 0 0);
    //     text-transform: uppercase;
    //     animation: textChay 1s linear forwards 1.5s;

    //     @media screen and (max-width: 1200px) {
    //         margin-bottom: 2rem;
    //     }
    // }

    &-bg {
        width: 100%;
        height: 100%;
        inset: 0;
        position: absolute;
        z-index: -1;

        @keyframes thuNho {
            0% {
                transform: scale(1.2);
            }
            50% {
                transform: scale(1.1);
            }
            100% {
                transform: scale(1);
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba($color: $color-second, $alpha: 0.7);
            z-index: 1;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;

            @media screen and (min-width: 1200px) {
                animation: thuNho 0.8s linear;
            }
        }
    }
}

.banner_tt {
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);
    height: calc(100vw * 400 / 1728);
    overflow: hidden;
    clip-path: inset(0 0 0);

    @media screen and (max-width: 1200px) {
        height: calc(100vw * 400 / 1200);
    }

    @media screen and (max-width: 600px) {
        height: calc(100vw * 400 / 800);
    }

    &--wrap {
        width: 100%;
        height: 100%;
    }

    &--img {
        position: fixed;
        inset: 0;

        width: 100vw;
        height: 100vh;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
