.about_our {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &--wrap {
        @media screen and (max-width: 600px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &--lf {
        &-wrap {
            min-height: 50.7rem;
            height: 100%;

            @media screen and (max-width: 1200px) {
                min-height: unset;
                height: 100%;
            }
        }

        &-list {
            position: relative;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: end;

            @media screen and (max-width: 1200px) {
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--rowContent0);
                row-gap: var(--rowContent1);
            }
        }

        &-img {
            padding-top: calc(100% * 507 / 374);
            width: 100%;
            display: block;
            overflow: hidden;
            position: relative;
            border-radius: var(--radius3);
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
            }
        }

        &-item {
            &:first-child {
                position: absolute;
                top: 0;
                left: 0;
                max-width: 37.4rem;
                width: 100%;
                height: 100%;


                .about_our--lf-img {
                    height: 100%;

                    @media screen and (max-width: 1200px) {
                        height: unset;
                        height: 100%;

                        padding-top: 100%;
                    }

                    @media screen and (max-width: 800px) {
                        padding-top: calc(100% * 520 / 1024);
                    }

                    @media screen and (max-width: 600px) {
                        padding-top: calc(100% * 400 / 600);
                    }
                }

                @media screen and (max-width: 1200px) {
                    position: relative;
                    width: calc(50% - var(--rowContent0));
                    height: unset;

                    max-width: none;
                    order: 1;
                }

                @media screen and (max-width: 800px) {
                    width: 100%;
                }

               
            }

            &:nth-child(2) {
                max-width: 23rem;
                width: 100%;
                margin-left: auto;

                .about_our--lf-img {
                    padding-top: calc(100% * 148 / 200);

                    @media screen and (max-width: 1200px) {
                        padding-top: 100%;
                    }
                }

                @media screen and (max-width: 1200px) {
                    position: relative;
                    //    max-width: none;
                    margin: 0 auto;
                    // max-width: none;
                    width: calc(50% - var(--rowContent0));
                    order: -1;
                    height: unset;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }

                @media screen and (max-width: 800px) {
                    width: 100%;
                    max-width: 35%;
                    margin: 0 auto;
                }

                @media screen and (max-width:600px) {
                    display: none;
                }
            }

            &:last-child {
                max-width: 48.8rem;
                width: 100%;
                margin-left: auto;

                .about_our--lf-img {
                    padding-top: calc(100% * 266 / 488);
                    border: 0.5rem solid $color-white;

                    @media screen and (max-width: 600px) {
                        padding-top: calc(100% * 400 / 600) !important;
                    }
                }

                @media screen and (max-width: 1200px) {
                    width: 100%;
                    order: 2;
                    max-width: none;
                    z-index: 2;
                    .about_our--lf-img {
                        padding-top: calc(100% * 520 / 1024);
                        border: none;
                        height: 100%;
                    }
                }
            }
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &--rt {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            height: 100%;

            @media screen and (max-width: 600px) {
                row-gap: var(--rowContent2);
            }

            .box-title {
                .title-cus {
                    margin: unset;

                    @media screen and (max-width: 460px) {
                        margin: 0 auto;
                    }
                }

                .title-lg {
                    text-align: start;

                    @media screen and (max-width: 460px) {
                        text-align: center;
                    }
                }
            }

            // @media screen and (max-width:1200px) {
            //     justify-content: center;
            //     // padding: var(--size-pd) 0;
            // }
        }

        &-list {
            margin-top: auto;
            display: flex;
            justify-content: space-between;
            column-gap: var(--columnContent3);
        }

        &-num {
            display: flex;
            position: relative;

            .num {
                @include fz-48;
                font-weight: 700;
                color: $color-second;
            }

            .plus {
                color: $color-pri;
                font-weight: 700;
                @include fz-60;
                top: -1.5rem;
                right: -4rem;
                line-height: 100%;
                position: absolute;
                line-height: 100%;
                @media screen and (max-width: 1200px) {
                    right: -3.5rem;
                }

                @media screen and (max-width: 800px) {
                    right: -2rem;
                    top: -0rem;
                    @include fz-40;
                }
            }
        }

        &-item {
            width: calc(50% - (var(--columnContent3) / 2));
            display: flex;
            // justify-content: center;
            align-items: center;
            flex-direction: column;
            padding: 1.6rem;
            border-radius: var(--radius1);
            border: 0.1rem solid #a1abab;

            @media screen and (max-width: 1200px) {
                padding: 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem;
            }

            .note-text {
                text-align: center;
            }
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }
}

.about_do {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pd);
    margin-top: var(--size-pd);
    // margin-bottom: var(--size-pd);

    // clip-path: inset(0 0 0);
    position: relative;

    &--bg {
        position: absolute;
        inset: 0;
        width: 100%;
        height: calc(50% + var(--size-pdl));
        display: block;
        z-index: -1;

        &::before {
            content: "";
            inset: 0;
            width: 100%;
            height: 100%;
            background: rgba($color: #000000, $alpha: 0.2);
            position: absolute;
            z-index: 1;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .box-title {
            .title-cus {
                background: rgba($color: #bbc4c4, $alpha: 0.3);
                color: $color-white;
                border: 0.1rem solid $color-white;
                &::before {
                    background: $color-white;
                }
            }
            .title-lg {
                color: $color-white;
            }
        }
    }

    &--list {
        .swiper {
            padding: 0.1rem 0;
        }
        .swiper-slide {
            height: auto;

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
        // margin-bottom: -10%;
    }

    &--item {
        &-ic {
            width: 4.8rem;
            height: 4.8rem;

            @media screen and (max-width: 1200px) {
                width: 4.2rem;
                height: 4.2rem;
            }

            @media screen and (max-width: 800px) {
                width: 3.6rem;
                height: 3.6rem;
            }

            @media screen and (max-width: 460px) {
                width: 3.2rem;
                height: 3.2rem;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        height: 100%;

        &-wrap {
            height: 100%;
            background: $color-white;
            padding: 2.4rem;
            border-radius: var(--radius1);
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.3);
            transition: 0.3s linear;

            .title-mn {
            }

            @include hover {
                background: $color-pri;

                .title-mn {
                    color: $color-white;
                }
                .note-text {
                    color: $color-white;
                }

                .about_do--item-ic {
                    img {
                        filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(3004%) hue-rotate(303deg)
                            brightness(121%) contrast(99%);
                    }
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 2.4rem 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.8rem 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.4rem 1.2rem;
            }

            @media screen and (max-width: 460px) {
                padding: 1rem 1.2rem;
            }
        }
    }
}

.about_prj {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .btn {
            margin: 0 auto;
        }
    }
}

.about_alb {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    overflow: hidden;
    &--title {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
        .splide {
            width: 100%;
        }

        .splide__slider {
            // width: 100%;
        }
    }

    &--btns {
        display: flex;
        column-gap: var(--columnContent1);
        width: fit-content;
        margin: 0 auto;
        .btn {
            &.actived {
                background: $color-pri;

                .btn-text {
                    color: $color-white;
                }
            }
            &-text {
                @include fz-16;
                font-weight: 700;
                text-transform: none;
            }
        }
    }

    &--list {
    }

    &--item {
        display: none;

        &.showed {
            // @keyframes anHienImg {
            //     0%{

            //         opacity: 0;
            //     }
            //     100%{
            //         opacity: 1;

            //     }
            // }

            display: block;
        }
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .splide__slide {
                height: auto;

                &:nth-child(odd) {
                    width: 20%;

                    .about_alb--item-img {
                        padding-top: calc(100% * 349 / 384);
                    }

                    @media screen and (max-width: 1200px) {
                        width: 25%;
                    }

                    @media screen and (max-width: 800px) {
                        width: 30%;
                    }

                    @media screen and (max-width: 600px) {
                        width: 35%;
                    }

                    @media screen and (max-width: 460px) {
                        width: 40%;
                    }
                    @media screen and (max-width: 360px) {
                        width: 45%;
                    }
                }

                &:nth-child(even) {
                    width: 30%;

                    .about_alb--item-img {
                        padding-top: calc(100% * 349 / 606);
                    }

                    @media screen and (max-width: 1200px) {
                        width: 35%;
                    }

                    @media screen and (max-width: 800px) {
                        width: 40%;
                    }

                    @media screen and (max-width: 600px) {
                        width: 45%;
                    }

                    @media screen and (max-width: 460px) {
                        width: 50%;
                    }

                    @media screen and (max-width: 360px) {
                        width: 55%;
                    }
                }
            }
        }

        &-img {
            display: block;
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
            border-radius: var(--radius3);
            cursor: pointer;
            @include hover {
                &::before {
                    opacity: 1;
                }
            }

            &::before {
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                inset: 0;
                background: rgba($color: #000000, $alpha: 0.5);
                opacity: 0;
                transition: 0.3s linear;
                z-index: 1;
            }

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
            }
        }
    }
}
