@import url("../assets/font/Helvetica/stylesheet.css");

* {
    box-sizing: border-box;
}

html {
    font-size: 10px;
    scroll-behavior: smooth;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }
}

body {
    font-family: "Helvetica";
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
    -webkit-tap-highlight-color: transparent;
    color: $color-text;
    background: $color-white;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }

    // @media screen and (max-width: 460px) {
    //     font-size: 14px;
    //   }
    &.actived {
        overflow: hidden;
    }
}

a {
    text-decoration: none;
    color: inherit;

    @include transition(0.3s);

    @include min(1200px) {
        &:hover {
            color: $color-pri;
        }
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active,
input:-webkit-autofill:focus {
    background-color: #ffffff !important;
    color: $color-text2 !important;
    -webkit-box-shadow: 0 0 0 1000px #ffffff inset !important;
    -webkit-text-fill-color: $color-text2 !important;
}

button,
select {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    cursor: pointer;
}

button {
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

input,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

button,
select,
input,
textarea {
    border: none;
    outline: none;
    box-shadow: none;
    font-family: var(--font-pri);
    color: black;
    background: transparent;
    font-family: var(--font-pri);
    &:focus {
        border: none;
        outline: none;
        box-shadow: none;
    }

    &:focus-visible {
        border: none;
        outline: none;
        box-shadow: none;
    }
}

::-webkit-scrollbar {
    width: 0.3rem;
}

::-webkit-scrollbar-thumb {
    background: $color-second;
}

::-webkit-scrollbar-track {
    background: $color-white !important;
    border-radius: 0.4rem !important;
}

@supports (-webkit-touch-action: none) {
    input,
    select,
    textarea {
        font-size: max(16px);
    }
}

.main {
    transition: 0.3s linear;
    &.pt {
        padding-top: var(--size-hd);
    }
}

.container {
    width: 100%;
    max-width: 124.5rem;
    height: 100%;
    margin: 0 auto;
    padding: 0 1.5rem;

    // @include max(800px) {
    //     padding: 0 0.8rem;
    // }

    // @include max(600px) {
    //     padding: 0 0.6rem;
    // }
}

.no-scroll {
    touch-action: none;
    -webkit-overflow-scrolling: none;
    overflow: hidden;
    overscroll-behavior: none;
}

iframe {
    vertical-align: middle;
}

img {
    max-width: 100%;
    height: auto;
}

.t-center {
    text-align: center;
}

.fw-b {
    font-weight: bold;
}

.fsi {
    font-style: italic;
}

.t-up {
    text-transform: uppercase;
}

.tabBtn {
    cursor: pointer;
}

.gItem {
    cursor: pointer;
}

.global-title {
    max-width: 0;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
}

.title {
    &-lg {
        @include fz-48;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-md {
        @include fz-40;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }

        strong {
            color: $color-pri;
        }
    }

    &-sm {
        @include fz-32;
    }

    &-sm2 {
        @include fz-36;
    }

    &-sm3 {
        @include fz-40;
        line-height: 1.3;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-sm4 {
        @include fz-48;
    }

    &-mn {
        @include fz-24;
    }

    &-mn2 {
        @include fz-28;
    }
}

.note {
    &-title {
        font-weight: 700;
        @include fz-16;
    }

    &-text {
        @include fz-16;
        strong {
            font-weight: 700;
        }

        @media screen and (max-width: 460px) {
        }
    }

    &-lg {
        @include fz-20;

        strong {
            color: $color-pri;
        }
    }

    &-lg2 {
        @include fz-24;

        strong {
            color: $color-pri;
        }
    }

    &-md {
        @include fz-18;
    }

    &-sm {
        @include fz-14;
    }

    &-smn {
        @include fz-12;
    }

    &-ic {
        width: 1.2rem;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        i {
            color: $color-pri;
            font-size: 1.4rem;
        }
    }
}

.fl {
    &-pri {
        font-family: "Helvetica";
    }
}

.cl {
    &-pri {
        color: $color-pri;
    }

    &-white {
        color: $color-white;
    }

    &-yellow {
        color: #f5d461;
    }

    &-second {
        color: $color-second;
    }

    &-text {
        color: $color-text;
    }

    &-text2 {
        color: $color-text2;
    }

    &-text3 {
        color: $color-text3;
    }

    &-gray {
        color: $color-gray;
    }

    &-gray2 {
        color: $color-gray2;
    }

    &-gray3 {
        color: $color-gray3;
    }

    &-gray4 {
        color: $color-gray4;
    }

    &-gray5 {
        color: $color-gray5;
    }

    &-black2 {
        color: $color-black-2;
    }

    &-black3 {
        color: $color-black-3;
    }

    &-black {
        color: $color-black;
    }

    &-blue {
        color: $color-blue;
    }

    &-blue2 {
        color: $color-blue2;
    }
}

.link-text {
    color: $color-second;
    @include fz-20;
    font-weight: 600;

    @include hover {
        color: $color-pri;
    }
}

.title-cus {
    padding: 1rem 1rem 1rem 3.5rem;
    border-radius: var(--radius1);
    background: $color-gray;
    font-weight: 700;
    color: $color-pri;
    width: fit-content;
    margin: 0 auto;
    position: relative;
    @include fz-16;
    line-height: 1.1;
    @media screen and (max-width: 600px) {
        @include fz-14;
        padding: 0.8rem 0.8rem 0.8rem 3rem;
    }

    &::before {
        content: "";
        position: absolute;
        width: 0.5rem;
        height: 0.5rem;
        border-radius: 50%;
        top: 48%;
        transform: translateY(-50%);
        left: 2rem;
        background: $color-pri;

        @media screen and (max-width: 600px) {
            left: 1.5rem;
        }
    }
}

.box-title {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent12);

    .title-lg {
        text-align: center;
    }

    .note-text {
        text-align: center !important;
        strong {
            font-weight: 700;
            color: $color-second;
        }
    }
}

.box-title2 {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent12);

    .title-cus {
        margin: unset;

        @media screen and (max-width:460px) {
            margin: 0 auto;
        }
    }
    .title-lg {
        text-align: start;
    }

    &-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: var(--columnContent2);
        flex-wrap: wrap;
        row-gap: var(---rowContent1);
        .box-navi {
            width: fit-content;
        }
    }
}

.lg-backdrop,
.lg-outer {
    z-index: 3000;
}

.box-info {
    display: flex;
    column-gap: 1rem;
    align-items: center;
    overflow: hidden;

    // @media screen and (max-width: 1200px) {
    //     column-gap: 0.8rem;
    // }

    &-author {
        // padding-right: 0.8rem;
        position: relative;
        // border-right: 0.1rem solid $color-text;
        line-height: 100%;
        &::before {
            @include fz-14;
            content: "|";
            color: $color-text2;
            border-radius: 50%;
            position: absolute;
            right: -0.8rem;
            top: 45%;
            transform: translateY(-50%);
        }

        // @media screen and (max-width: 1200px) {
        //     right: -0.6rem;
        // }

        .note-mn {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            white-space: nowrap;
            color: $color-second;
            @include fz-14;

            @media screen and (max-width: 860px) {
                @include fz-12;
            }
        }

        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            white-space: nowrap;
            color: $color-second;

            @include fz-14;

            @media screen and (max-width: 860px) {
                @include fz-12;
            }
        }
    }

    &-date {
        display: flex;
        column-gap: 0.8rem;
        row-gap: 0.2rem;
        align-items: center;
        // flex-wrap: wrap;
        .note-mn {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            color: $color-text2;

            top: 0.1rem;
            position: relative;
            // white-space: nowrap;
            @include fz-14;

            @media screen and (max-width: 860px) {
                @include fz-12;
            }
        }

        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            // white-space: nowrap;
            color: $color-text2;

            @include fz-14;

            @media screen and (max-width: 860px) {
                @include fz-12;
            }
        }
    }
}

.box-dm {
    color: $color-text2;
    background: $color-white2;
    padding: 0.3rem 1.7rem;
    border-radius: 0.6rem;
    font-weight: 400;
    width: fit-content;
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 0.4rem;
    @include hover {
        background: $color-pri;
        color: $color-white;

        .note-sm {
            color: $color-white;
        }
    }

    @media screen and (max-width: 1200px) {
        padding: 0.4rem 1rem;
        border-radius: 0.4rem;
    }

    @media screen and (max-width: 800px) {
        padding: 0.2rem 0.8rem;
    }

    @media screen and (max-width: 600px) {
        padding: 0.2rem 0.6rem;
    }

    @media screen and (max-width: 460px) {
        padding: 0.2rem 0.6rem;
    }

    &-ic {
        width: 1.4rem;
        height: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        display: none;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @media screen and (max-width: 600px) {
            width: 1.2rem;
            height: 1.2rem;
        }
    }

    .note-sm {
        // line-height: 100%;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        display: flex;
        align-items: center;
        top: 0.1rem;
        position: relative;
        @include fz-14;
        color: $color-text2;
        font-weight: 400;
    }
}

.box-info2 {
    display: flex;
    column-gap: 1.6rem;
    align-items: center;
    overflow: hidden;
    margin-top: 1rem;
    flex-wrap: wrap;
    row-gap: 0.8rem;
    @media screen and (max-width: 1200px) {
        column-gap: 0.4rem;
    }

    &-author {
        padding-right: 1.6rem;
        border-right: 0.1rem solid $color-gray4;
        display: flex;
        column-gap: 0.8rem;
        align-items: center;
        @media screen and (max-width: 1200px) {
            padding-right: 0.4rem;
            column-gap: 0.4rem;
        }

        @media screen and (max-width: 460px) {
            border-right: unset;
        }

        .ic {
            width: 2rem;
            height: 2rem;
            flex-shrink: 0;
            line-height: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            white-space: nowrap;

            @media screen and (max-width: 1200px) {
                @include fz-12;
            }
        }
    }

    &-date {
        display: flex;
        column-gap: 0.8rem;
        row-gap: 0.2rem;
        // align-items: center;
        // flex-wrap: wrap;

        // padding-right: 1.6rem;
        // border-right: 0.1rem solid $color-gray4;
        align-items: center;

        .ic {
            width: 2rem;
            height: 2rem;
            flex-shrink: 0;
            line-height: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        @media screen and (max-width: 1200px) {
            padding-right: 0.4rem;
        }

        @media screen and (max-width: 460px) {
            border-right: unset;
        }
        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            color: $color-text;
            // white-space: nowrap;

            @media screen and (max-width: 1200px) {
                @include fz-12;
            }
        }
    }

    &-time {
        display: flex;
        column-gap: 0.8rem;
        align-items: center;

        @media screen and (max-width: 1200px) {
            column-gap: 0.4rem;
        }

        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }

        .ic {
            width: 2rem;
            height: 2rem;
            flex-shrink: 0;
            line-height: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }
}

.control {
    &-1 {
        &-list {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
        }

        &-item {
        }
    }

    &-2 {
        &-list {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
        }

        &-item {
        }
    }

    &-img {
        width: 3.2rem;
        height: 3.2rem;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        transition: 0.3s ease;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        position: relative;
        background: $color-white;
        border: 0.1rem solid $color-gray4;
        &.heart {
            input {
                display: none;

                &:checked {
                    & ~ .ic {
                        opacity: 1;
                    }
                }
            }

            .ic {
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 50%;
                background-image: url(../assets/images/s1.1.svg);
                position: absolute;
                inset: 0;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                z-index: 2;
                opacity: 0;
                transition: 0.3s ease;
            }

            img {
                position: relative;
            }
        }

        @include hover {
            transform: scale(1.2);

            box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.3);
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            transition: 0.3s ease;
        }

        @media screen and (max-width:600px) {
            width: 2.8rem;
            height: 2.8rem;
        }
    }
}

.fancybox__container {
    z-index: 3000;
}
